@use "sass:map";

// global > breakpoint
// ------------------------------
$breakpoints: (
    'md': 'only screen and (max-width: 768px)',
    'xl': 'screen and (max-width: 1025px)'
);

@mixin mq ($breakpoint: md) {
    @media #{map-get($breakpoints, $breakpoint)} {
        @content;
    }
}