
@use "./global" as g;
.privacy_page{
    padding-bottom: 122px;
    @media only screen and (max-width: 768px) {
        padding-bottom: 70px;
    }
}
.privacy_intro {
    @media only screen and (max-width: 768px) {
        margin: 0;
        padding: 30px 0 0;
    }
    &_desc01{
        margin: -29px 0 46px;
        font-size: g.rem(15);
        font-weight: 500;
        letter-spacing: 0.05em;
        color: #000;
        @media only screen and (max-width: 768px) {
            font-size: g.rem(13);
            margin:0 0 17px;
        }
    }
    &_ttl01{
        font-size: g.rem(18);
        font-weight: 500;
        letter-spacing: 0.05em;
        margin-bottom: 12px;
        color: #000;
        @media only screen and (max-width: 768px) {
            font-size: g.rem(14);
            margin-bottom: 13px;
        }
    }
    &_desc02{
        font-size: g.rem(15);
        font-weight: 400;
        letter-spacing: 0.05em;
        margin-bottom: 35px;
        line-height: 1.8em;
        @media only screen and (max-width: 768px) {
            font-size: g.rem(13);
            margin-bottom: 30px;
        }
    }
}

