@use "sass:math";
@use "./global" as g;
.koutou_page{
    .c_txt_color01{
        color: #8DB10B;
    }
    .c_txt_color02{
        color: #DD6E1F;
    }
    .c_txt_color03{
        color: #51BEC8;
    }
    .c_txt_color04{
        color: #6CBC64;
    }
    .c_txt_color05{
        color: #CC7645;
    }
    .c_txt_color06{
        color: #FA7158;
    }
    .c_txt_color07{
        color: #51BEC8;
    }
}
.koutou_intro{
    padding: 0 0 g.clamped(90,131,769,1500);
    @media only screen and (max-width: 768px) {
        padding: 0 0 60px;
    }
    .c_headding_line {
        margin:-9px 0 23px;
        .txt_ttl{
            padding-bottom: 21px;
            @media only screen and (max-width: 768px) {
                letter-spacing: 0.05em;
                padding-bottom: 8px;
            }
        }
        .txt_desc{
            font-weight: 500;
            letter-spacing: 0.05em;
            margin-top: 20px;
        }
    }
    &_list{
        display: flex;
        justify-content: space-between;
        @media only screen and (max-width: 768px) {
            flex-wrap: wrap;
        }
    }
    &_item{
        width: 48.5%;
        background: #F5F5F5;
        padding: 32px 41px 22px;
        @media only screen and (max-width: 768px) {
            width: 100%;
            margin-bottom: 20px;
            padding: 30px 20px;
            &:last-child{
                margin-bottom: 0;
            }
        }
        .ttl{
            font-family: 'Zen Kaku Gothic New', sans-serif;
            font-weight: bold;
            letter-spacing: 0.05em;
            font-size: g.rem(18);
            line-height: 1.5em;
            margin-bottom: 14px;
            @media only screen and (max-width: 768px) {
                font-size: g.rem(20);
            }
        }
        .desc{
            font-size: g.rem(15);
            font-weight: 400;
            letter-spacing: 0.05em;
            @media only screen and (max-width: 768px) {
                font-weight: 500;
                font-size: g.rem(13);
            }
        }
        &:last-child{
            .ttl{
                margin-top: -7px;
                @media only screen and (max-width: 768px) {
                    margin-top: 0;
                }
            }
        }
    }
}

.koutou_schedule{
    padding: 0 0 60px;
    @media only screen and (max-width: 768px) {
        padding:0 0 30px;
    }
    .c_headding_line01{
        margin-bottom: 40px;
    }
    &_item{
        display: flex;
        align-items: center;
        padding-bottom: 61px;
        @media only screen and (min-width: 769px) {
            &:nth-child(2){
                .koutou_schedule_info_desc{
                    margin-bottom: 13px;
                }
            }
            &:nth-child(3){
                .koutou_schedule_info_desc{
                    margin-bottom: 0;
                }
            }
        }
        @media only screen and (max-width: 768px) {
            justify-content: space-between;
            align-items: flex-start;
            position: relative;
            padding-bottom: 40px;
            &::after{
                position: absolute;
                content: '';
                top: 45px;
                left: 22px;
                background: #23A4BD;
                width: 2px;
                height: 100%;
            }
            &:last-child{
                &::after{
                    content: none;
                }
            }
        }
        &:last-child{
            padding-bottom: 0;
            .koutou_schedule_circle{
                &::after{
                    content: none;
                }
            }
        }
   
        @media only screen and (max-width: 768px) {
            &:nth-child(1), &:nth-child(3){
                .koutou_schedule_circle{
                    padding-top: 2px;
                }
            }
    
        }
    }
    &_circle{
        position: relative;
        width: 157px;
        height: 159px;
        border-radius: 78px;
        background: #23A4BD;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 10px;
        text-align: center;
        margin: 0 30px 0 0;
        flex-shrink: 0;
        @media only screen and (max-width: 768px) {
            width: 48px;
            height: 48px;
            padding: 0;
            margin: 0;
        }
        &::after{
            position: absolute;
            content: '';
            top: 100%;
            left: 50%;
            transform: translateX(-50%);
            background: #23A4BD;
            width: 4px;
            height: 100%;
            @media only screen and (max-width: 768px) {
                content: none;
            }
        }
        .ttl{
            color: #fff;
            letter-spacing: 0.04em;
            font-family: 'Barlow', sans-serif;
            font-weight: 500;
            font-size: g.rem(22);
            letter-spacing: 0.05em;
            @media only screen and (max-width: 768px) {
                font-size: g.rem(10);
                display: inline-block;
                line-height: 1.2em;
            }
        }
        .ttl_f_jost{
            font-family: 'Jost', sans-serif;
        }
        &_title{
            text-align: center;
        }
    }
    &_info{
        @media only screen and (max-width: 768px) {
            width: calc(100% - 60px);
        }
        &_desc{
            font-size: g.rem(16);
            font-weight: 400;
            letter-spacing: 0;
            color: #000;
            margin-bottom: 9px;
            @media only screen and (max-width: 768px) {
                font-size: g.rem(13);
                font-weight: 500;
                letter-spacing: 0.05em;
            }
        }
        &_txts{
            margin-bottom: 20px;
            li{
                letter-spacing: 0.05em;
                font-size: g.rem(13);
                font-weight: 400;
                color: #2A2C2C;
                line-height: 1.75em;
                @media only screen and (max-width: 768px) {
                    font-size: g.rem(12);
                }
            }
        }
    }
    .c_list_number{
        .item{
            margin-bottom: 0;
            line-height: 1.7em;
            font-size: g.rem(16);
            letter-spacing: 0;
            padding-left: 16px;
            @media only screen and (max-width: 768px) {
                font-size: g.rem(13);
                font-weight: 500;
                letter-spacing: 0.05em;
            }
        }
    }
    .c_btn01{
        @media only screen and (max-width: 768px) {
            margin-left: 0;
        }
    }
}

.koutou_exemption {
    padding: 79px 0 50px;
    @media only screen and (max-width: 768px) {
        padding: 30px 0;
    }
    &_accor{
        margin-bottom: 18px;
        &:last-child{
            margin-bottom: 0;
        }
        &.bg01{
            .koutou_exemption_accor_ttl{
                background: #0C63A3;
            }
        }
        &.bg02{
            .koutou_exemption_accor_ttl{
                background: #8DB10B;
            }
        }
        &.bg03{
            .koutou_exemption_accor_ttl{
                background: #DD6E1F;
            }
        }
    }
    &_accor_ttl{
        position: relative;
        background: #0C63A3;
        padding: 28px 70px 28px 27px;
        @media only screen and (max-width: 768px) {
        padding: 27px 60px 27px 14px;
        }
        .ttl{
            font-size: g.rem(22);
            font-weight: bold;
            letter-spacing: 0.08em;
            color: #fff;
            @media only screen and (max-width: 768px) {
                font-size: g.rem(20);
            }
        }
        @media only screen and (min-width: 769px) {
            transition: all 0.5s;
            &:hover{
                cursor: pointer;
                opacity: 0.8;
            }
        }
        &::before, &::after{
            position: absolute;
            content:'';
            top: calc(50% + 1px);
            transform: translateY(-50%);
            background: #fff;
            transition: all 0.5s;
        }
        &::before{
            height: 2px;
            width: 17px;
            right: 29px;
            @media only screen and (max-width: 768px) {
                right: 15px;
            }
        }
        &::after{
            right: 8px;
            width:2px;
            height: 17px;
            right: 37px;
            @media only screen and (max-width: 768px) {
                right: 23px;
            }
        }
        &.is_open{
            &::after{
                transform: translateY(-50%) rotate(90deg);
            }
        }
    }
    &_accor_wrapper{
        display: none;
        background: #EFEFEF;
        border-radius: 0 0 10px 10px;
        padding: 48px g.clamped(20,52,769,1024) 56px;
        @media only screen and (max-width: 768px) {
            padding: 30px 15px;
        }
    }
    &_accor_wrapper_ttl{
        font-size: g.rem(18);
        font-weight: 500;
        letter-spacing: 0.08em;
        color: #2A2C2C;
        line-height: 1.5em;
        margin-bottom: 15px;
        @media only screen and (max-width: 768px) {
            padding: 0 10px;
        }
    }
    &_accor_list{
        margin-bottom: 56px;
        @media only screen and (max-width: 768px) {
            padding: 0 10px;
            margin-bottom: 30px;
        }
        li{
            @media only screen and (max-width: 768px) {
                margin-bottom: 5px;
                &:last-child{
                    margin-bottom: 0;
                }
            }
        }
        .txt01, .txt02, .txt_char{
            font-size: g.rem(15);
            font-weight: 500;
            letter-spacing: 0.08em;
            display: inline-block;
            @media only screen and (max-width: 768px) {
                font-size: g.rem(13);
                line-height: 1.75em;
            }
            img{
                display: none!important;
            }
        }
        .txt01{
            color: #2A2C2C;
            @media only screen and (max-width: 1024px) {
                display: block;
            }
            @media only screen and (max-width: 768px) {
                display: inline-block;
                margin-bottom: 5px;
            }
        }
        .txt_char{
            margin: 0 16px;
            @media only screen and (max-width: 1024px) {
                margin: 0 5px 0 0;
            }
            @media only screen and (max-width: 768px) {
                margin: 0 20px;
            }
            &::after{
                position: relative;
                content: '';
                top: 0;
                left: 0;
                font-family: 'Noto Sans JP', sans-serif;
                font-weight: 500;
                font-size: g.rem(16);
                @media only screen and (max-width: 768px) {
                    font-size: g.rem(13);
                }
            }
        }
        .txt02{
            @media only screen and (max-width: 768px) {
                display: block;
                padding-left: 15px;
            }
        }
    }
    &_accor_content{
        padding: 47px g.clamped(30,40,769,1500) 38px;
        border-radius: 10px;
        background: #fff;
        margin-bottom: 31px;
        @media only screen and (max-width: 768px) {
            padding: 22px 15px 40px;
        }
        &:last-child{
            margin-bottom: 0;
            @media only screen and (min-width: 768px) {
                padding: 52px 40px 30px;
                .koutou_exemption_accor_headings{
                    margin-bottom: 35px;
                }
            }
        }
    }
    &_accor_headings{
        display: flex;
        align-items: center;
        margin-bottom: 41px;
        @media only screen and (max-width: 768px) {
            flex-wrap: wrap;
            margin-bottom: 15px;
        }
        .num{
            border-radius: 50%;
            background: #FA7158;
            color: #fff;
            font-family: 'Jost', sans-serif;
            font-size: g.rem(14);
            letter-spacing: 0;
            line-height: 1;
            width: 27px;
            height: 27px;
            flex-shrink: 0;
            display: inline-flex;
            justify-content: center;
            align-items: center;
            text-align: center;
            margin: 0 10px 0 0;
            @media only screen and (max-width: 768px) {
                font-size: g.rem(11);
                padding: 1px 2px 0 0;
            }
        }
        .head{
            display: inline-block;
            font-size: g.rem(20);
            font-weight: 500;
            letter-spacing: 0.05em;
            line-height: 1.2em;
            @media only screen and (max-width: 768px) {
                font-size: g.rem(16);
            }
        }
        &.color01{
            .head, .ttl{
                color: #FA7158;
            }
            .num{
                background: #FA7158;
            }
        }
        &.color02{
            .head, .ttl{
                color: #23A4BD;
            }
            .num{
                background: #23A4BD;
            }
        }
        .ttl{
            font-size: g.rem(15);
            font-weight:500;
            letter-spacing: 0.08em;
            line-height: 1.2em;
            display: inline-block;
            margin: 0 2px 0 0;
            @media only screen and (max-width: 768px) {
                font-size: g.rem(13);
                font-weight: 500;
                flex-shrink: 0;
            }
        }
        .txt{
            margin-bottom: 0;
            line-height: 1.2em;
            display: inline-block;
            color: #333;
            font-size: g.rem(15);
            font-weight: 400;
            letter-spacing: 0.08em;
            @media only screen and (max-width: 768px) {
                font-size: g.rem(13);
                letter-spacing: 0.05em;
                font-weight: 500;
            }
        }
        .wrap_heading01{
            @media only screen and (max-width: 768px) {
                display: flex;
            }
        }
    }
    &_accor_heading01{
        display: flex;
        align-items: center;
        margin: 0 24px 0 0;
        @media only screen and (max-width: 768px) {
            margin: 0 0 5px 0;
            width: 100%;
        }
    }
    &_accor_heading02{
        font-size: g.rem(16);
        font-weight: bold;
        line-height: 1.5em;
        color: #2A2C2C;
        margin-bottom: 10px;
        letter-spacing: 0.08em;
        @media only screen and (max-width: 768px) {
            font-size: g.rem(13);
            font-weight: bold;
        }
    }
    &_accor_heading03{
        margin: 0 15px 0 0;
        @media only screen and (max-width: 768px) {
            display: block;
            margin: 0 0 5px 0;
        }
        .head{
            font-size: g.rem(16);
            font-weight: bold;
            line-height: 1.5em;
            color: #333;
            letter-spacing: 0.08em;
            @media only screen and (max-width: 768px) {
                font-size: g.rem(13);
            }
        }
    }
    &_accor_headings02{
        display: flex;
        margin-bottom: 7px;
        @media only screen and (max-width: 768px) {
            flex-wrap: wrap;
            margin-bottom: 15px;
        }
        .txt{
            font-size: g.rem(15);
            font-weight: 500;
            line-height: 1.5em;
            color: #333;
            letter-spacing: 0.08em;
            margin-bottom: 0;
            @media only screen and (max-width: 768px) {
                font-size: g.rem(12);
            }
        }
    }
    &_sec{
        margin-bottom: 48px;
        @media only screen and (max-width: 768px) {
            margin-bottom: 35px;
        }
        &:last-child{
            margin-bottom: 0;
        }
        .desc{
            margin-top: -34px;
            line-height: 1.2em;
            font-size: g.rem(13);
            font-weight: 400;
            letter-spacing: 0.08em;
            @media only screen and (max-width: 768px) {
                margin-top: 0;
                font-size: g.rem(10);
            }
        }
        .desc01{
            font-size: g.rem(13);
            font-weight: 400;
            letter-spacing: 0.08em;
            @media only screen and (max-width: 768px) {
                font-size: g.rem(10);
            }
        }
    }
    &_list_dot{
        margin-bottom: 11px;
        li{
            font-size: g.rem(13);
            font-weight: 400;
            letter-spacing: 0.08em;
            color: #333;
            margin: 0 0 -2px;
            @media only screen and (max-width: 768px) {
                font-size: g.rem(10);
                margin: 0;
            }
        }
    }
}

.koutou_qa{
    padding: g.clamped(30,82,769,1500) 0 g.clamped(60,100,769,1500);
    .c_headding_line01{
        letter-spacing: 0.05em;
        margin-bottom: 51px;
    }
    &_list{
        margin-bottom: 25px;
    }
    &_item{
        counter-increment: number;
        margin-bottom: 49px;
        @media only screen and (max-width: 768px) {
            margin-bottom: 30px;
        }
        &:last-child{
            margin-bottom: 0;
        }
    }
    &_ttl{
        position: relative;
        display: flex;
        align-items: flex-start;
        color: #000;
        font-size: g.rem(20);
        font-weight: 500;
        letter-spacing: 0.12em;
        line-height: 1.5em;
        margin-bottom: 18px;
        @media only screen and (max-width: 768px) {
            font-size: g.rem(16);
            margin-bottom: 10px;
        }
        &::before {
            font-family: "Barlow", sans-serif;
            content: "Q" counter(number);
            position: relative;
            top: -4px;
            left: 6px;
            font-size: g.rem(20);
            font-weight: 600;
            letter-spacing: 0.04em;
            line-height: 1.5em;
            margin: 0 34px 0 0;
            @media only screen and (max-width: 768px) {
                margin: 0 20px 0 0;
                top: -2px;
                left: 0;
            }
       } 
    }
    &_desc{
        position: relative;
        display: flex;
        align-items: flex-start;
        color: #000;
        font-size: g.rem(15);
        font-weight: 400;
        letter-spacing: 0;
        @media only screen and (max-width: 768px) {
            font-size: g.rem(13);
            font-weight:500;
            letter-spacing: 0.05em;
        }
        &::before {
            font-family: "Barlow", sans-serif;
            content: "A" counter(number);
            position: relative;
            top: -4px;
            left: 6px;
            font-size: 20px;
            font-weight: 600;
            letter-spacing: 0.04em;
            line-height: 1.5em;
            margin: 0 34px 0 0;
            color: transparent;
            @media only screen and (max-width: 768px) {
                margin: 0 20px 0 0;
            }
       } 
    }
}

