.koutou_contact{
    background: #F5F5F5;
    padding:40px 30px;
    @media only screen and (max-width: 768px) {
        padding: 40px 22px;
    }
    &_ttl{
        text-align: center;
        margin-bottom: 10px;
        @media only screen and (max-width: 768px) {
            max-width: 222px;
            margin: 0 auto 15px;
        }
        .wrap_ttl{
            display: inline-block;
            text-align: center;
            padding: 5px 10px;
            background: #23A4BD;
            font-size: g.rem(17);
            color: #fff;
            width: 286px;
            border-radius: 19px;
            font-weight: bold;
            @media only screen and (max-width: 768px) {
                width: 100%;
                font-size: g.rem(13);
            }
        }
    }
    &_tel{
        text-align: center;
        margin-bottom: 4px;
        @media only screen and (max-width: 768px) {
            margin-bottom: 10px;
        }
        a{
            position: relative;
            text-decoration:none;
            font-family: 'Barlow', sans-serif;
            font-weight: 600;
            font-size: g.rem(45);
            color: #2A2C2C;
            display: inline-block;
            padding-left: 41px;
            line-height: 1.5em;
            letter-spacing: 0.1em;
            @media only screen and (max-width: 768px) {
                font-size: g.rem(28);
                padding-left: 28px;
            }
            &::before{
                position: absolute;
                top: 12px;
                left: 11px;
                content: "";
                background: url(../images/entrance/koutou/icon_tel.svg) no-repeat center/contain;
                width: 20px;
                height: 46px;
                @media only screen and (max-width: 768px) {
                    width: 14px;
                    height: 32px;
                    left: 0;
                    top: 8px;
                }
            }
            @media only screen and (min-width: 769px) {
                pointer-events: none;
            }
        }
    }
    &_desc{
        text-align: center;
        color: #2A2C2C;
        font-size: g.rem(16);
        font-weight: 400;
        letter-spacing: 0.1em;
        margin-bottom: 20px;
        line-height: 1.5em;
        @media only screen and (max-width: 768px) {
            font-size: g.rem(13);
            font-weight: 500;
            letter-spacing: 0.05em;
            margin-bottom: 15px;
            line-height: 1.7em;
        }
    }
    &_bg{
        text-align: center;
        max-width: 752px;
        margin: 0 auto;
        .link{
            display: flex;
            align-items: center;
            justify-content: center;
            text-align: center;
            text-decoration:none;
            padding:22px 10px 16px;
            background: #FFF000;
            @media only screen and (min-width: 769px) {
                transition: all 0.5s;
                &:hover{    
                    opacity: 0.8;
                }
            }
            @media only screen and (max-width: 768px) {
                padding: 10px 15px;
            }
        }
       .txt{
            position: relative;
            text-align: center;
            margin-bottom: 0;
            font-size: g.rem(14);
            font-weight: 400;
            letter-spacing: 0;
            color: #2A2C2C;
            display: inline-block;
            letter-spacing: 0.15em;
            @media only screen and (max-width: 768px) {
                font-size: g.rem(12);
                letter-spacing: 0.05em;
                text-align: left;
                padding-right: 15px;
            }
            &::before{
                position: absolute;
                right: -9px;
                top: 5px;
                content: '';
                background: url(../images/entrance/koutou/icon_link.svg) no-repeat center/contain;
                width: 11px;
                height: 11px;
                @media only screen and (max-width: 768px) {
                    right: 0;
                    top: 8px;
                }
            }
       }
    }

}
