@use "sass:math";
@use "./global" as g;

.entrance_loan_page {
  .c_nav_anchor {
    margin: 0 auto 83px;
    @include g.mq() {
      margin: 30px auto 46px;
    }
  }

  .c_headding_line.is_under .txt_ttl {
    padding-bottom: 20px;
    @include g.mq() {
      padding-bottom: 12px;
    }
  }

  .loan {
    &_main {
        padding-bottom: 120px;
        @include g.mq() {
            padding-bottom: 80px;
        }
    }
    &_sec {
      &:not(.first) {
        margin-top: 70px;
        @include g.mq() {
            margin-top: 77px;
        }
      }
    }
    &_list {
      margin-top: 30px;
    }

    &_item {
      &:not(:last-child) {
        margin-bottom: 60px;
        @include g.mq() {
            margin-bottom: 24px;
        }
      }
    }

    &_ttl {
      letter-spacing: 0.08em;
      font-weight: bold;
      line-height: math.div(32, 22);
      padding: 30px 42px;
      font-size: g.rem(22);
      background-color: #d5d5d5;
      @include g.mq() {
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        font-size: g.rem(18);
        padding: 18px 22px;
      }
    }

    &_board {
      background-color: #f5f5f5;
      padding: 40px 41px;
      padding: 40px clamp(20px, 30vw - 250px, 41px);
      border-bottom-left-radius: 10px;
      border-bottom-right-radius: 10px;
      @include g.mq() {
        padding: 20px 22px;
      }
    }

    &_head {
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      @include g.mq() {
        display: block;
      }
      &_img {
        max-width: 383px;
        margin: 0 20px 0 0;
        @include g.mq() {
          max-width: 100%;
          width: 100%;
          margin-right: 0;
          img {
            width: 100%;
          }
        }
      }

      &_desc {
        max-width: 501px;
        letter-spacing: 0.05em;
        font-weight: 400;
        line-height: math.div(28, 14);
        margin-top: -4px;
        padding-right: 5px;
        @include g.mq() {
            max-width: 100%;
          font-size: g.rem(13);
          line-height: math.div(23, 13);
          padding: 0;
          margin-top: 14px;
        }
      }
    }

    &_contact {
      a[href^="tel"] {
        pointer-events: none;

        @include g.mq() {
          pointer-events: all;
        }
      }

      margin-top: 40px;

      &_item {
        display: flex;
        background-color: white;
        min-height: 130px;
        align-items: center;
        padding: 15px 0;
        @include g.mq() {
          display: block;
          padding: 15px 18px 16px;
        }
        &:not(:last-child) {
          margin-bottom: 20px;
          @include g.mq() {
            margin-bottom: 40px;
          }
        }
        &.temp1 {
          min-height: 137px;
          @include g.mq() {
            min-height: 0;
          }
        }
        &.temp2 {
          .loan_contact_head {
            padding: 0 10px 0 20px;
            padding-left: clamp(10px, 30vw - 250px, 20px);
            @include g.mq() {
                padding: 0 0 10px;
            }
          }
        }
        &.temp3 {
          min-height: 166px;
          @include g.mq() {
            position: relative;
            padding-bottom: 97px;
          }
          .loan_contact_head {
            padding-left: 34px;
            align-items: start;
            font-size: g.rem(17);
            min-height: 114px;
            width: calc(100% - 540px);
            min-width: 160px;
            @include g.mq() {
                min-width: 0;
                min-height: 0;
                width: 100%;
                padding: 0;
                font-size: g.rem(15);
                padding-bottom: 10px;
            }
          }
          .tel_wrap {
            position: relative;
            top: -5px;
            @include g.mq() {
                position: static;
            }
          }
          .c_btn {
            // position: relative;
            // top: 3px;
            @include g.mq() {
                position: absolute;
                bottom: 22px;
                left: 50%;
                transform: translateX(-50%);
                z-index: 1;
                top: auto;

            }
          }
          .tel_num {
            margin-right: 23px;
            @include g.mq() {
                margin: 0;
            }
          }
        }
      }

      &_content {
        padding: 0 30px 0 30px;
        width: 100%;
        padding: 0 clamp(20px, math.div(30, 1000) * 100vw, 30px);
        @include g.mq() {
          padding: 0;
          margin-top: 13px;
        }
        .txt_desc {
          font-size: g.rem(15);
          font-weight: 500;
          line-height: math.div(28, 15);
          letter-spacing: 0.05em;
          padding-left: 8px;
          @include g.mq() {
            padding: 0;
          }
        }

        .tel_num {
          margin-right: 33px;
          @include g.mq() {
            margin-right: 0;
          }
          &::before {
            height: 30px;
            position: relative;
            top: 4px;
            width: 22px;
            background-image: url("../images/entrance/loan/icon_tel.svg");
            height: clamp(25px, math.div(30, 1000) * 100vw, 30px);
            width: clamp(16px, math.div(22, 1000) * 100vw, 22px);
            @include g.mq() {
                width: 17px;
                height: 30px;
            }
          }
        }

        .tel_info {
          margin-top: 18px;
          @include g.mq() {
            margin-top: 14px;
          }
          ul {
            display: flex;
            letter-spacing: 0.05em;
            font-weight: bold;
            margin-top: 6px;
            @include g.mq() {
                display: block;
                line-height: math.div(20,14);
                margin-top: 10px;
            }
            & > *:not(:last-child) {
              margin-right: 29px;
              @include g.mq() {
                margin: 0 0 12px;
              }
            }
          }
          .txt_color {
            color: #23a4bd;
          }
        }
        .tel_txt {
          font-size: g.rem(15);
          font-weight: 400;
          letter-spacing: 0.05em;
          @include g.mq() {
            font-weight: 500;
            line-height: math.div(28,15);
          }
        }

        .tel_ttl {
          margin-bottom: 8px;
          position: relative;
          top: 2px;
          @include g.mq() {
            position: static;
            text-align: center;
            line-height: math.div(21,15);
          }
        }

        .tel_wrap {
          display: flex;
          flex-wrap: wrap;
          align-items: center;
          @include g.mq() {
            display: block;
          }
        }

        .c_btn {
          width: auto;
            @include g.mq() {
                min-width: 236px;
                min-width: math.div(236,375)*100vw;
                width: math.div(236,375)*100vw;
                max-width: 400px;
                margin: 0 auto;
            }
          a {
            width: auto;
            padding: 0 45px;
            height: 36px;
            font-size: g.rem(14);
            letter-spacing: 0.08em;
            font-weight: 500;
            background-color: #23a4bd;
            background-image: none;
            margin-top: 5px;
            @include g.mq() {
                margin-top: 0;
                width: 100%;
                height: 44px;
                padding: 0;
            }
            &::after {
              content: "";
              display: block;
              height: 13px;
              width: 13px;
              margin-left: 7px;
              background-size: contain;
              background-position: center;
              background-repeat: no-repeat;
              background-image: url("../images/entrance/loan/icon_blank.svg");
            }
          }
        }
      }

      .tel_ttl {
        font-size: g.rem(15);
        letter-spacing: 0.05em;
        font-weight: bold;
      }

      .tel_num {
        display: flex;
        letter-spacing: 0.05em;
        font-size: g.rem(28);
        line-height: math.div(40, 28);
        align-items: center;
        font-weight: bold;
        font-size: clamp(g.rem(24), math.div(28, 1000) * 100vw, g.rem(28));
        @include g.mq() {
          font-size: g.rem(21);
          font-size: min(g.rem(28), math.div(28,375)*100vw);
          justify-content: center;
          margin-bottom: 29px;
        }
        &::before {
          content: "";
          display: block;
          margin-right: 10px;
          background-size: contain;
          background-position: center;
          background-repeat: no-repeat;
        }
      }

      &_head {
        text-align: center;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        letter-spacing: 0.05em;
        font-weight: bold;
        max-width: 317px;
        flex-shrink: 0;
        width: 38%;
        font-size: g.rem(18);
        min-height: 77px;
        border-right: 1px solid #a5a4a4;
        font-size: clamp(g.rem(16), math.div(18, 900) * 100vw, g.rem(18));
        @include g.mq() {
          max-width: 100%;
          width: 100%;
          padding-bottom: 10px;
          border-right: none;
          border-bottom: 1px solid #a5a4a4;
          display: block;
          font-size: g.rem(15);
          height: auto;
          min-height: 0;
          font-weight: 500;
        }
        .tel_ttl {
          // margin-left: 29px;
          text-align: center;
          margin: 0;
        }

        .tel_num {
          padding-left: 5px;
          margin-top: 5px;
          @include g.mq() {
            margin: 10px auto 0;
            padding: 0;
          }
          &::before {
            height: 21px;
            width: 32px;
            background-image: url("../images/entrance/loan/icon_label_tel.svg");
          }
        }
      }

      &_notice {
        font-size: g.rem(13);
        font-weight: 400;
        line-height: math.div(19, 13);
        margin-top: 15px;
      }
    }
    &_over {
      overflow: auto;
      @include g.mq() {
        overflow-x: scroll;
        padding-bottom: 10px;
        &::-webkit-scrollbar {
          height: 10px !important;
          -webkit-overflow-scrolling: auto;
          background: transparent !important;
        }

        /* Track */
        &::-webkit-scrollbar-track {
          -webkit-border-radius: 5px !important;
          border-radius: 5px !important;
          background: #e6e6e6 !important;
          -webkit-box-shadow: none !important;
        }

        /* Handle */
        &::-webkit-scrollbar-thumb {
          -webkit-border-radius: 5px !important;
          border-radius: 5px !important;
          background: #a5a4a4 !important;
          -webkit-box-shadow: none !important;
        }
        &::-webkit-scrollbar-thumb:window-inactive {
          background: #a5a4a4 !important;
        }
      }
    }
    &_table {
      margin-top: 40px;

      &_notice {
        font-size: g.rem(13);
        font-weight: 400;
        letter-spacing: 0.06em;
        margin-top: 16px;
        line-height: math.div(19, 13);
        @include g.mq() {
            font-weight: 500;
            line-height: math.div(23,13);
            letter-spacing: 0.05em;
        }
      }

      table {
        width: 100%;
        min-width: 914px;
      }

      table,
      tr,
      td {
        border-collapse: collapse;
      }

      tr {
        &:not(:last-child) {
          border-bottom: 1px dashed #a5a4a4;
        }
      }

      &.table1 {
        td {
          padding: 14px 10px 14px 18px;

          &:first-child {
            text-align: center;
          }
        }
      }

      td {
        font-size: g.rem(13);
        font-weight: 400;
        padding: 20px;
        line-height: math.div(19, 13);
        letter-spacing: 0.05em;

        &.col_head {
          text-align: center;
          font-weight: 500;
        }

        background-color: white;

        &:first-child {
          background-color: #d5d5d5;
          width: 16.4%;
          font-weight: 500;
          &.col_extra {
            width: 178px;
          }
        }

        &:nth-child(3) {
          border-left: 1px dashed #a5a4a4;
          width: 40.6%;
        }
      }
    }

    &_headline {
      font-weight: 400;
      line-height: math.div(28, 14);
      letter-spacing: 0.05em;
      margin-bottom: -5px;
      @include g.mq() {
        font-weight: 500;
        font-size: g.rem(13);
        margin-top: 0;
        line-height: math.div(23,13);
      }
    }
    &_btn {
      display: flex;
      justify-content: space-between;
      @include g.mq {
        display: block;
        margin-top: 46px;
      }
      &>* {
        max-width: calc(100%/3 - 5px);
        @include g.mq() {
            width: 100%;
            &:not(:last-child) {
                margin-bottom: 20px;
            }
        }
      }
      .c_btn {
        @include g.mq() {
            width: math.div(294,375)*100vw;
                max-width: 400px;
        }
        a {
            white-space: nowrap;
            @include g.mq() {
                height: 60px;
                width: 100%;
                
            }
          }
      }
      
    }
  }
}
