@use "./global" as g;
@use "_l_common";
.c_sp{
    display: none;
}
@media only screen and (max-width: 768px) {
    // #tmp_header, #tmp_footer, .bread_clumb, #tmp_bl9{
    //     display: none;
    // }
    .c_pc{
        display: none;
    }
    .c_sp{
        display: inherit;
    }
}
.c_container{
    max-width: 1040px;
    padding: 0 20px;
    margin: 0 auto;
   @media only screen and (max-width: 768px) {
    padding:0 min((20/320)*100vw,25px);
   }
}
.c_pages{
    ~ #tmp_bl9{
        margin-top: 0!important;
    }
    .main_txt{
        width: 100%;
        h2{
            font-weight: bold;
            letter-spacing: 0.1em;
            @media only screen and (max-width: 768px) {
                font-size: g.rem(24)!important;
            }
        }
        p{
            font-weight: 600;
            color: #23A4BD!important;
            letter-spacing: 0.05em;
            @media only screen and (max-width: 768px) {
                font-size: g.rem(14)!important;
                letter-spacing: 0.1em;
                font-weight:500;
            }
        }
    }

    #tmp_mainvisual{
        @media only screen and (max-width: 768px) {
            height: 145px;
            &.c_mainvisual{
                height: auto;
            }
        }
       .c_container{
        height: 100%;
       } 
    }

}

.c_list_number {
    counter-reset: list-num;
    .item {
        position: relative;
        padding-left:22px;
        margin-bottom: 15px;
        font-size: g.rem(15);
        letter-spacing: 0.05em;
        line-height: 1.8em;

        @media only screen and (max-width: 768px) {
            font-size: g.rem(13);
        }
        &:last-child {
            margin-bottom: 0;
        }
        &::before {
            position: absolute;
            left: 0;
            top: 0;
            background: none;
            border: 0;
            counter-increment: list-num;
            content: counter(list-num) '.';
        }
    }
}
.c_btn{
    width: g.clamped(250,320,769,1500);
    @media only screen and (max-width: 768px) {
        width: 294px;
        margin: 0 auto;
    }
    @media only screen and (max-width: 320px) {
        width: 100%;
    }
    a{
        text-decoration:none;
        padding: 18px 10px;
        border-radius: 30px;
        background: linear-gradient(45deg, #23A4BD, #0D8DB7);
        font-size: g.rem(16);
        font-weight: 500;
        letter-spacing: 0.05em;
        display: flex;
        justify-content: center;
        align-items: center;
        color: #fff;
        line-height: 1.5em;
        transition: all 0.5s;
        @media only screen and (min-width: 769px) {
            &:hover{
                box-shadow: 0px 2px 1px 0px #023241;
            }
        }
        @media only screen and (max-width: 768px) {
            font-size: g.rem(14);
            padding: 15px 10px;
        }
    }
    &.style01{
        a{
            background: #fff;
            color: #23A4BD;
            @media only screen and (min-width: 769px) {
                &:hover{
                    color: #fff;
                    background: linear-gradient(45deg, #23A4BD, #0D8DB7);
                }
            }
        }
    }
}
.c_btn01{
    width: g.clamped(300,319,769,1500);
    @media only screen and (max-width: 768px) {
        width: 294px;
        margin: 0 auto;
    }
    @media only screen and (max-width: 375px) {
        width: 100%;
    }
    a{
        position: relative;
        text-decoration:none;
        padding: 15px 10px;
        border-radius: 10px;
        background: #23A4BD;
        font-size: g.rem(13);
        font-weight: 500;
        letter-spacing: 0.1em;
        display: flex;
        justify-content: center;
        align-items: center;
        color: #fff;
        line-height: 1.5em;
        transition: all 0.5s;
        @media only screen and (min-width: 769px) {
            &:hover{
                box-shadow: 0px 2px 1px 0px #023241;
            }
        }
        @media only screen and (max-width: 768px) {
            font-size: g.rem(12);
            padding: 12px 10px;
            letter-spacing: 0.1em;
            font-weight: 500;
        }
        &::after{
            position: absolute;
            content: '';
            top: 50%;
            transform: translateY(-50%);
            right: 20px;
            background: url(../images/entrance/koutou/icon_arrow.svg) no-repeat center/contain;
            width: 5px;
            height: 11px;
        }
    }
}
.c_intro{
    .c_container{
        max-width: 828px;
    }
    &_heading{
        color: #262626;
        font-family: 'Zen Kaku Gothic New', sans-serif;
        font-weight: bold;
        font-size: g.rem(30);
        letter-spacing: 0.1em;
        line-height: 1.5em;
        margin-bottom: 24px;
        @media only screen and (max-width: 768px) {
            font-size: g.rem(22);
        }
    }
    &_desc{
        color: #000;
        font-weight: 400;
        letter-spacing: 0.1em;
        font-size: g.rem(18);
        line-height: 2em;
        @media only screen and (max-width: 768px) {
            font-size: g.rem(13);
            font-weight: 500;
        }
    }
}
.c_headding_line01{
    font-family: 'Zen Kaku Gothic New', sans-serif;
    font-size: g.rem(25);
    font-weight: 500;
    letter-spacing: 0.1em;
    line-height: 1.44em;
    padding-bottom: 24px;
    border-bottom: solid 1px #A5A4A4;
    margin-bottom: 30px;
    @media only screen and (max-width: 768px) {
        font-size: g.rem(22);
        letter-spacing: 0.05em;
        font-weight:bold;
        padding-bottom: 8px;
    }
}

.c_tbl_scroll{

    @media only screen and (max-width: 1024px) {
        overflow-x: auto;
        padding-bottom: 25px;
        margin-bottom: 30px;
        &:last-child{
            margin-bottom: 0;
        }
        &::-webkit-scrollbar-track {
          border-radius: 5px;
          background-color: #e6e6e6;
        }
        &::-webkit-scrollbar {
          height: 10px;
          background-color: transparent;
        }
        &::-webkit-scrollbar-thumb {
          border-radius: 5px;
          -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
          background-color: #a5a4a4;
        }
        table{
            width: 100%;
            min-width: 914px;
        }
    }
    @media only screen and (max-width: 768px) {
        padding-bottom: 15px;
        table{
            min-width: 795px;
        }
    }
}