@use "sass:math";
@use "./global" as g;
@use "_policy";
@use "_form";
@use "_loan";
// @use "template_open_campus";

@include g.mq() {
    #tmp_header, #tmp_footer {
        display: none;
    }
}



.c_headding_line {
  margin-bottom: 40px;
  @include g.mq() {
    margin-bottom: 28px;
  }
  &.is_above {
    padding-top: 49px;
    position: relative;
    border-top: 1px solid #d5d5d5;
    @include g.mq() {
      padding-top: 30px;
    }
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      transform: translateY(-50%);
      width: 200px;
      height: 6px;
      background-color: #23a4bd;
      @include g.mq() {
        width: 120px;
      }
    }
  }
  &.is_under {
    margin-bottom: 26px;
    @include g.mq() {
      margin-bottom: 37px;
    }
    .txt_ttl {
    //   border-bottom: 2px solid #262626;
    border-bottom: 2px solid #A5A4A4;
      padding-bottom: 8px;
      font-weight: 500;
      @include g.mq() {
        font-weight: bold;
      }
    }
    .txt_desc {
      margin-top: 23px;
      @include g.mq() {
        margin-top: 14px;
        letter-spacing: 0.05em;
        font-weight: 500;
      }
    }
  }
  .txt_ttl {
    font-size: g.rem(30);
    font-weight: bold;
    letter-spacing: 0.1em;
    display: block;
    font-family: "Zen Kaku Gothic New", sans-serif;
    @include g.mq() {
      font-size: g.rem(22);
    }
  }
  .txt_desc {
    font-size: g.rem(16);
    letter-spacing: 0.1em;
    display: block;
    font-weight: 400;
    line-height: math.div(28, 16);
    margin-top: 12px;
    @include g.mq() {
      font-size: g.rem(13);
    }
  }
}

.c_list_btn {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 21px 40px;
  margin-top: 42px;
  column-gap: clamp(20px, 30vw - 250px, 40px);
  @include g.mq() {
    margin-top: 28px;
    grid-template-columns: repeat(1, 1fr);
  }
  .btn_rect {
    &_gray {
      min-height: 138px;
      display: flex;
      color: white;
      background-color: #262626;
      align-items: center;
      justify-content: space-between;
      padding: 10px 26px;
      padding: 10px clamp(20px, 30vw - 250px, 26px);
      transition: opacity 0.5s ease;
      &:hover {
        opacity: 0.8;
      }
      @include g.mq() {
        min-height: 130px;
        padding: 10px 25px;
        position: relative;
      }
      &::after {
        content: "";
        display: block;
        height: 35px;
        width: 35px;
        background-repeat: no-repeat;
        background-image: url(../images/entrance/icon_arrow.png);
        background-size: contain;
        background-position: center;
        flex-shrink: 0;
        margin-left: 20px;
        @include g.mq() {
          content: none;
        }
      }
    }
    &_txt {
      @include g.mq() {
        width: 100%;
      }
      & > * {
        display: block;
        letter-spacing: 0.1em;
      }
      .txt_ttl {
        font-size: g.rem(20);
        font-weight: bold;
        @include g.mq() {
          position: relative;
          display: flex;
          justify-content: space-between;
          &::after {
            content: "";
            display: block;
            background-repeat: no-repeat;
            background-image: url(../images/entrance/icon_arrow.png);
            background-size: contain;
            background-position: center;
            flex-shrink: 0;
            margin-left: 10px;
            height: 26px;
            margin-top: 4px;
            width: 26px;
          }
        }
      }
      .txt_desc {
        font-size: g.rem(14);
        font-weight: 500;
        margin-top: 10px;
        @include g.mq() {
          font-size: g.rem(13);
        }
      }
    }
  }
}

.c_banner_screen {
  display: flex;
  position: relative;
  display: block;
  &:hover {
    .banner_img img {
      transform: scale(1.05);
    }
  }
  .banner {
    &_img {
      margin: 0;
      overflow: hidden;
      img {
        width: 100%;
        transition: transform 0.5s ease;
      }
      @include g.mq() {
        img {
          max-height: 660px;
          object-fit: cover;
          object-position: 50% 40%;
        }
      }
    }
    &_inner {
      max-width: 930px;
      padding: 0 30px;
      margin: 0 auto;
      position: absolute;
      bottom: 60px;
      width: 100%;
      z-index: 1;
      left: 50%;
      transform: translateX(-50%);
      display: flex;
      align-items: flex-end;
      justify-content: space-between;
      @include g.mq() {
        padding: 0 25px;
        bottom: 27px;
      }
      &::after {
        content: "";
        display: block;
        height: 53px;
        width: 53px;
        background-repeat: no-repeat;
        background-image: url(../images/entrance/icon_arrow_md.svg);
        background-size: contain;
        background-position: center;
        flex-shrink: 0;
        margin-left: 20px;
        @include g.mq() {
          content: none;
        }
      }
    }
    &_content {
      margin: 0;
      color: white;
    }
    &_ttl {
      font-size: g.rem(30);
      letter-spacing: 0.1em;
      font-weight: bold;
      margin-bottom: 10px;
      font-family: "Zen Kaku Gothic New", sans-serif;
      @include g.mq() {
        font-family: "Noto Sans JP", sans-serif;
        font-size: g.rem(20);
        position: relative;
        margin-bottom: 7px;
        &::after {
          content: "";
          display: block;
          height: 26px;
          width: 26px;
          background-repeat: no-repeat;
          background-image: url(../images/entrance/icon_arrow_md.svg);
          background-size: contain;
          background-position: center;
          flex-shrink: 0;
          margin-left: 20px;
          position: absolute;
          top: 4px;
          right: 0;
        }
      }
      .f_en {
        font-family: "Barlow", sans-serif;
      }
    }
    &_desc {
      font-weight: 400;
      letter-spacing: 0.1em;
      font-size: g.rem(16);
      margin: 0;
      @include g.mq() {
        font-weight: 500;
        font-size: g.rem(13);
      }
    }
  }
}

