.c_mainvisual {
  position: relative;
  z-index: 0;
  margin-bottom: 97px;
  @include g.mq() {
    margin-bottom: 52px;
  }
  &_img {
    margin: 0;
    width: 100%;
    img {
      min-height: 260px;
      object-fit: cover;
      width: 100%;
      object-position: center;
      @include g.mq() {
        max-height: 380px;
        min-height: 0;
      }
    }
  }
  &_content {
    color: white;
    max-width: 1160px;
    padding: 0 20px;
    position: absolute;
    z-index: 1;
    width: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding-top: 20px;
    @include g.mq() {
      padding-top: 11px;
    }
  }
  &_ttl {
    font-size: g.rem(46);
    font-weight: bold;
    letter-spacing: 0.1em;
    line-height: 1;
    font-family: "Zen Kaku Gothic New", sans-serif;
    @include g.mq() {
      font-family: "Noto Sans JP", sans-serif;
      font-size: g.rem(24);
      text-align: center;
    }
  }
  &_desc {
    font-size: g.rem(18);
    font-weight: 500;
    font-family: "Barlow", sans-serif;
    text-transform: uppercase;
    letter-spacing: 0.05em;
    margin-top: 10px;
    @include g.mq() {
      font-size: g.rem(14);
      text-align: center;
      margin-top: 8px;
      letter-spacing: 0.1em;
    }
  }
}

.c_nav_anchor {
  display: flex;
  margin: 35px 0 68px;
  @include g.mq() {
    margin: 31px auto 46px;
    display: block;
    width: math.div(240, 375) * 100vw;
    min-width: 240px;
  }
  .btn_archor {
    height: 72px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    border-right: 1px solid #a5a4a4;
    padding: 0 10px;
    min-width: max-content;
    margin: 0;
    &:hover {
        &.is_blue {
            color: rgba(12, 99, 163, 0.7);
          }
          &.is_green {
            color: rgba(141, 177, 11, 0.7);
          }
          &.is_orange {
            color: rgba(221, 110, 31, 0.7);
          }
    }
    @include g.mq() {
      flex-direction: row;
      border-width: 0px !important;
      justify-content: space-between;
      border-style: solid;
      height: auto;
      border-color: #a5a4a4 !important;
      padding: 11px 15px 10px;
      border-bottom-width: 1px !important;
    }
    &:first-child {
      border-left: 1px solid #a5a4a4;
      @include g.mq() {
        border-top-width: 1px !important;
      }
    }
    &_txt {
      font-weight: 500;
      font-size: g.rem(20);
      letter-spacing: 0.06em;
      @include g.mq() {
        font-size: g.rem(14);
        letter-spacing: 0.1em;
        font-weight: bold;
      }
    }
    &_arrow {
      height: 23px;
      width: 23px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: #23a4bd;
      transform: rotate(90deg);
      margin-top: 10px;
      @include g.mq() {
        margin: 0;
        width: 22px;
        height: 22px;
      }
      &::after {
        height: 8px;
        width: 11px;
        content: "";
        display: block;
        background-repeat: no-repeat;
        background-image: url(../images/entrance/icon_arrow_down.svg);
        background-size: contain;
        background-position: center;
      }
    }
    &.is_blue {
      color: #0c63a3;
      .btn_archor_arrow {
        background-color: #0c63a3;
      }
    }
    &.is_green {
      color: #8db10b;
      .btn_archor_arrow {
        background-color: #8db10b;
      }
    }
    &.is_orange {
      color: #dd6e1f;
      .btn_archor_arrow {
        background-color: #dd6e1f;
      }
    }
  }
}
