@use "./global" as g;
.school_page{
    padding-bottom: 100px;
    background: #EFEFEF;
    @media only screen and (max-width: 768px) {
        padding-bottom: 30px;
    }
    .c_mainvisual{
        margin-bottom: 0;
    }
    .c_mainvisual_img{
        margin-bottom: 0;
    }
    .c_intro{
        background: #fff;
        padding: 80px 0 84px;
        @media only screen and (max-width: 768px) {
            padding: 40px 0 60px;
        }
    }
}
.school_learning{
    background: url(../images/school/bg_school_learning01.jpg) no-repeat center/cover;
    padding:103px 0 102px;
    @media only screen and (max-width: 768px) {
        background: url(../images/school/bg_school_learning_sp01.jpg) no-repeat center/cover;
        padding:60px 0;
    }
    .c_container{
        display: flex;
        justify-content: space-between;
        @media only screen and (max-width: 768px) {
            flex-wrap: wrap;
        }
    }
    &_img{
        width:51%;
        flex-shrink: 0;
        @media only screen and (max-width: 768px) {
            width: 100%;
            text-align: center;
            margin-bottom: 30px;
        }
    }
    &_info{
        width: 44%;
        padding-top: 13px;
        @media only screen and (max-width: 768px) {
            padding-top: 0;
            width: 100%;
        }
    }
    &_heading{
        font-family: 'Zen Kaku Gothic New', sans-serif;
        color: #fff;
        font-size: g.rem(33);
        font-weight: bold;
        margin-bottom: 15px;
        line-height: 1.5em;
        letter-spacing: 0.1em;
        @media only screen and (max-width: 1024px) {
            letter-spacing: 0.05em;
        }
        @media only screen and (max-width: 768px) {
            letter-spacing: 0.1em;
            font-size: g.rem(22);
        }
    }
    &_desc{
        font-size: g.rem(16);
        font-weight: 400;
        letter-spacing: 0.1em;
        margin-bottom: 38px;
        color: #fff;
        line-height: 1.75em;
        @media only screen and (max-width: 768px) {
            font-size: g.rem(13);
            letter-spacing: 0.05em;
            margin-bottom: 20px;
        }
    }
}
.school_history{
    background: #fff;
    padding:109px 0 146px;
    @media only screen and (max-width: 768px) {
        padding: 60px 0;
    }
    .c_list_btn{
        margin:41px 0 90px;
        @media only screen and (max-width: 768px) {
            margin: 0 0 30px;
        }
    }
    @media only screen and (max-width: 768px) {
        .c_headding_line {
            .txt_desc{
                letter-spacing: 0.05em;
                font-weight: 500;
            }
        }
        .c_banner_screen{
            .banner_desc{
                font-weight:500;
            }
        }
    }
}

.school_sns{
    background: #EFEFEF;
    padding: 106px 0 36px;
    position: relative;
    @media only screen and (max-width: 768px) {
        padding: 60px 0;
        .c_headding_line {
            .txt_ttl{
                letter-spacing: 0.05em;
            }
            .txt_desc{
                font-weight: 500;
            }
        }
    }
    
    .c_container{
        position: relative;
        z-index: 3;
    }
    .c_headding_line{
        margin-bottom: 48px;
        @media only screen and (max-width: 768px) {
            margin-bottom: 35px;
        }
    }
    &::before{
        position: absolute;
        top: 0;
        right: 0;
        content: "";
        clip-path: polygon(100% 0, 0 0, 100% 100%);
        -webkit-clip-path: polygon(100% 0, 0 0, 100% 100%);
        background: #EFEFEF;
        width: clamp(150px, 13.6798905609vw + 44.8016415869px, 250px);
        height: clamp(150px, 13.6798905609vw + 44.8016415869px, 250px);
        mix-blend-mode: multiply;
        -webkit-mix-blend-mode: multiply;
        z-index: 1;
        @media only screen and (max-width: 768px) {
            width: 63px;
            height: 63px;
        }
    }
    &::after{
        position: absolute;
        top: 0;
        left: 0;
        content: "";
        background: rgba(239, 239, 239, 0.4);
        mix-blend-mode: multiply;
        -webkit-mix-blend-mode: multiply;
        clip-path: polygon(100% 0, 0 0, 0 100%);
        -webkit-clip-path: polygon(100% 0, 0 0, 0 100%);
        z-index: 0;
        height: g.clamped(600,709,769,1500);
        width: 100%;
        @media only screen and (max-width: 768px) {
            height: 178px;
        }

    }
    &_list{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: flex-start;
        @media only screen and (max-width: 768px) {
            
        }
    }
    &_item{
        position: relative;
        width: 48.4%;
        background: #fff;
        border-radius: 10px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 36px 30px 37px 30px;
        margin-bottom: 24px;
        min-height: 231px;
        @media only screen and (max-width: 1024px) {
            padding: 36px 20px 37px 20px;
        }
        @media only screen and (max-width: 768px) {
            flex-wrap: wrap;
            width: 100%;
            min-height: 210px;
            padding: 25px min((20/320)*100vw,25px) 28px min((20/320)*100vw,25px);
            &:last-child{
                margin-bottom: 0;
            }
        }
    }
    &_icon{
        width: 23%;
        margin: 0;
        @media only screen and (max-width: 1024px) {
            width: 15%;
        }
        @media only screen and (max-width: 768px) {
            width: auto;
            position: absolute;
            top: 13%;
            right: min((20/320)*100vw,25px);
        }
        @media only screen and (max-width: 375px) {
            top: 10%;
        }
        img{
            max-width: 64px;
            margin: 0 auto;
            @media only screen and (max-width: 1024px) {
                max-width: 50px;
            }
        }
    }
    &_info{
        width: 70.6%;
        @media only screen and (max-width: 1024px) {
            width: 80%;
        }
        @media only screen and (max-width: 768px) {
            width: 100%;
        }
    }
    &_ttl{
        margin-bottom: 5px;
        @media only screen and (max-width: 768px) {
            margin-bottom: 20px;
            width: calc(100% - 60px);
            word-break: break-all;
        }
        .wrap_ttl{
            font-size: g.rem(20);
            font-weight:500;
            letter-spacing: 0.06em;
            color: #2A2C2C;
            display: inline-block;
            line-height: 1.5em;
            @media only screen and (max-width: 768px) {
                word-wrap: break-word;
            }
        }
        .ttl_link{
            @media only screen and (min-width: 769px) {
                transition: all 0.5s;
                &:hover{
                    opacity: 0.8;
                }
            }
            .wrap_ttl{
                position: relative;
                padding-left: 30px;
                &::before{
                    position: absolute;
                    top: 7px;
                    left: 0;
                    content: '';
                    background: url(../images/school/icon_arrow04.svg) no-repeat center/contain;
                    width: 18px;
                    height: 18px;
                }   
            }
        }
    }
    &_desc{
        color: #2A2C2C;
        font-size: g.rem(16);
        letter-spacing: 0;
        font-weight: 400;
        @media only screen and (max-width: 768px) {
            font-size: g.rem(13);
            letter-spacing: 0.05em;
        }
    }
    &_links{
        display: flex;
        flex-wrap: wrap;
        margin-top: 19px;
    }
    &_link_txt{
        position: relative;
        font-size: g.rem(16);
        font-weight: 400;
        letter-spacing: 0.06em;
        line-height: 1.5em;
        display: inline-block;
        padding-left: 28px;
        @media only screen and (min-width: 769px) {
            transition: all 0.5s;
            &:hover{
                opacity: 0.8;
            }
        }
        @media only screen and (max-width: 768px) {
            font-size: g.rem(14);
            padding-left: 25px;
        }
        &::before{
            position: absolute;
            top: 4px;
            left: 0;
            content: '';
            width: 18px;
            height: 18px;
            @media only screen and (max-width: 768px) {
                width: 16px;
                height: 16px;
                top: 2px;
            }
        }   
    }
    &_link{
        margin: 0 g.clamped(10,19,1025,1500) 0 0;
        @media only screen and (max-width: 768px) {
            margin: 0 28px 0 0;
        }
        &:last-child{
            margin: 0;
        }
        &01{
            .school_sns_link_txt{
                color: #0C63A3;
                &::before{
                    background: url(../images/school/icon_arrow01.svg) no-repeat center/contain;
                }
            }
        }
        &02{
            .school_sns_link_txt{
                color: #8DB10B;
                &::before{
                    background: url(../images/school/icon_arrow02.svg) no-repeat center/contain;
                }
            }
        }
        &03{
            .school_sns_link_txt{
                color: #DD6E1F;
                &::before{
                    background: url(../images/school/icon_arrow03.svg) no-repeat center/contain;
                }
            }
        }
    }
    &_item02{
        .school_sns_icon {
            @media only screen and (max-width: 375px) {
                top: 7%;
            }
        }
    }
    &_item03{
        padding: 36px 30px 45px 36px;
        @media only screen and (max-width: 1024px) {
            padding: 36px 20px 37px 20px;
        }
        @media only screen and (max-width: 768px) {
            padding: 25px min((20/320)*100vw,25px) 28px min((20/320)*100vw,25px);
        }
        .school_sns_info {
            width: 71.5%;
            @media only screen and (max-width: 1024px) {
                width: 80%;
            }
            @media only screen and (max-width: 768px) {
                width: 100%;
            }
        }        
        .school_sns_icon {
            @media only screen and (max-width: 768px) {
                top: 21%;
            }
            img{
                position: relative;
                top: 6px;
                @media only screen and (max-width: 768px) {
                    top: 0;
                }
            }
        }
    }
    &_item04{
        padding: 43px 30px 40px 36px;
        @media only screen and (max-width: 1024px) {
            padding: 36px 20px 37px 20px;
        }
        @media only screen and (max-width: 768px) {
            padding: 25px min((20/320)*100vw,25px) 28px min((20/320)*100vw,25px);
        }
        .school_sns_info {
            width: 71.5%;
            @media only screen and (max-width: 1024px) {
                width: 80%;
            }
            @media only screen and (max-width: 768px) {
                width: 100%;
            }
        }        
        .school_sns_icon {
            @media only screen and (max-width: 768px) {
                top: 21%;
            }
            @media only screen and (max-width: 375px) {
                top: 18%;
            }
            img{
                position: relative;
                top: -1px;
                @media only screen and (max-width: 768px) {
                    top: 0;
                }
            }
        }
    }
    &_item05{
        padding: 39px 30px 45px 36px;
        @media only screen and (max-width: 1024px) {
            padding: 36px 20px 37px 20px;
        }
        @media only screen and (max-width: 768px) {
            padding: 25px min((20/320)*100vw,25px) 28px min((20/320)*100vw,25px);
        }
        .school_sns_info {
            width: 71.5%;
            @media only screen and (max-width: 1024px) {
                width: 80%;
            }
            @media only screen and (max-width: 768px) {
                width: 100%;
            }
        }        
        .school_sns_icon {
            @media only screen and (max-width: 768px) {
                top: 16%;
            }
            img{
                position: relative;
                top: 7px;
                @media only screen and (max-width: 768px) {
                    top: 0;
                }
            }
        }
    }
}