@use "sass:math";
@use "./global" as g;
.interviews_page
{
    padding-bottom: g.clamped(200,261,769,1500);
    @media only screen and (max-width: 768px) {
        padding-bottom: 70px;
    }
    .c_mainvisual{
        margin-bottom: 99px;
        @media only screen and (max-width: 768px) {
            margin-bottom: 60px;
        }
    }
}
.interviews{
    &_item{
        position: relative;
        display: flex;
        justify-content: space-between;
        max-width: 1040px;
        padding: 0 20px;
        margin: 0 auto;
        margin-bottom: g.clamped(200,247,769,1500);
        @media only screen and (max-width: 768px) {
            margin-bottom: 60px;
            padding:0 min((20/320)*100vw,25px);
            flex-wrap: wrap;
        }
        &:nth-child(even){
            flex-direction: row-reverse;
            @media only screen and (max-width: 768px) {
                flex-direction: row;
            }
            .interviews_img{
                left: auto;
                right: calc(50% - 31px);
                @media only screen and (max-width: 768px) {
                    right: 0;
                    left: 0;
                }
            }
            .interviews_info{
                padding-left: g.clamped(0,79,769,1500);
                @media only screen and (max-width: 768px) {
                    padding-left: 0;
                }
            }
        }
        &:last-child{
            margin-bottom: 0;
        }
    }
    &_img{
        position: absolute;
        top: 0;
        left: calc(50% - 31px);
        width: 75%;
        line-height: 0;
        @media only screen and (max-width: 768px) {
            width: 500%;
            left: 0;
            position: relative;
            order: 1;
            margin-bottom: 20px;
            text-align: center;
        }
    }
    &_heading{
        margin-bottom: 34px;
        @media only screen and (max-width: 768px) {
            margin-bottom: 8px;
        }
        .heading_en{
            font-family: 'Barlow', sans-serif;
            font-size: g.rem(20);
            font-weight: 500;
            letter-spacing: 0.1em;
            color: #23A4BD;
            display: block;
            line-height: 1.5em;
            text-transform: uppercase;
            margin-bottom: 5px;
            @media only screen and (max-width: 768px) {
                font-size: g.rem(12);
            }
        }
        .heading_jp{
            font-family: 'Zen Kaku Gothic New', sans-serif;
            font-weight: bold;
            font-size: g.rem(38);
            letter-spacing: 0.15em;
            color: #2A2C2C;
            display: block;
            line-height: 1.5em;
            @media only screen and (max-width: 768px) {
                font-size: g.rem(22);
            }
        }
    }
    &_desc{
        font-size: g.rem(16);
        font-weight: 400;
        color: #262626;
        margin-bottom: 36px;
        letter-spacing: 0.1em;
        @media only screen and (max-width: 768px) {
            font-size: g.rem(13);
            margin-bottom: 30px;
        }
    }
    &_info{
        padding-top: g.clamped(30,102,769,1500);
        width: 40%;
        @media only screen and (max-width: 768px) {
            width: 100%;
            padding: 0;
            order: 2;
        }
    }
}