@use "sass:math";
@use "global" as g;

.entrance_appform_page {
    .c_sp {
        display: none !important;
        @include g.mq() {
            display: inherit !important;
        }
    }
    .c_pc {
        @include g.mq() {
            display: none !important;
        }
    }
    .c_headding_line.is_under  {
        margin-bottom: 49px;
        @include g.mq() {
            margin-bottom: 37px;
        }
        .txt_ttl {
            border-color: #A5A4A4;
            padding-bottom: 22px;
            @include g.mq() {
                padding-bottom: 11px;
            }
        }
        .txt_desc {
            font-size: g.rem(18);
            margin-top: 26px;
            font-weight: 500;
            @include g.mq() {
                font-size: g.rem(13);
                margin-top: 14px;
            }
        }
    }
    .appform {
        &_main {
            padding-bottom: 100px;
            @include g.mq() {
                padding-bottom: 80px;
            }
        }
        &_bg {
            background-color: #F5F5F5;
            padding: 100px 0;
            @include g.mq() {
                padding: 33px 0;
            }
        }
        &_sec {
            &:not(.appform_bg) {
                margin-top: 108px;
                @include g.mq() {
                    margin-top: 75px;
                }
            }
        }
        &_list {
            margin-top: 56px;
            @include g.mq() {
                margin-top: 37px;
            }
        }
        &_item {
            background-color: white;
            display: flex;
            align-items: center;
            @include g.mq() {
                flex-direction: column-reverse;
            }
            &:not(:last-child) {
                margin-bottom: 30px;
            }
        }
        &_img {
            flex-shrink: 0;
            max-width: 362px;
            margin: 0;
            img{
                width: 100%;
            }
            @include g.mq() {
                width: 100%;
                max-width: 100%;
            }
        }
        &_ttl {
            font-weight: bold;
            font-size: g.rem(24);
            letter-spacing: 0.1em;
            line-height: math.div(38,24);
            font-size: clamp(g.rem(20), math.div(24, 900)*100vw, g.rem(24));
            @include g.mq() {
                font-size: g.rem(22);
                line-height: math.div(38,22);
            }
        }
        &_desc {
            font-size: g.rem(18);
            letter-spacing: 0.05em;
            line-height: math.div(28,18);
            font-weight: 400;
            margin-top: 17px;
            font-size: clamp(g.rem(16), math.div(18, 900)*100vw, g.rem(18));
            @include g.mq() {
                font-weight: 500;
                font-size: g.rem(15);
                line-height: math.div(25,15);
                letter-spacing: 0.1em;
                margin-top: 8px;
            }
            
        }
        &_content {
            padding-left: 49px;
            margin-right: 34px;
            padding-left: clamp(20px, 30vw - 250px, 49px);
            margin-right: clamp(25px, 30vw - 250px, 34px);
            width: 100%;
            @include g.mq() {
                padding: 30px 23px;
                margin: 0;
            }
        }
        &_btn {
            margin-top: 19px;
            @include g.mq() {
                margin-top: 24px;
            }
            a {
                font-size: g.rem(13);
                height: 49px;
                border-radius: 5px;
                letter-spacing: 0.1em;
                position: relative;
                width: 100%;
                background-color: #23A4BD;
                background-image: none;
                &::after {
                    content: "";
                    position: absolute;
                    width: 8px;
                    height: 14px;
                    background-repeat: no-repeat;
                    background-size: contain;
                    background-position: center;
                    background-image: url("../images/entrance/icon_arrow2.svg");
                    top: 50%;
                    transform: translateY(-50%);
                    z-index: 1;
                    right: 17px;
                }
                @include g.mq() {
                    height: auto;
                    line-height: math.div(19,13);
                    min-height: 70px;
                }
            }
            .c_btn {
                max-width: 320px;
                width: 100%;
                // @include g.mq() {
                //     max-width: 300px;
                // }
            }
        }
    }
}
