@use "sass:math";
@use "global" as g;

.entrance_app_page {
  .c_nav_anchor .btn_archor {
    border-color: black;
  }
  .c_headding_line.is_under .txt_ttl {
    border-bottom: 2px solid #262626;
  }
  table,
  td,
  th,
  tr {
    border-collapse: collapse;
  }

  table {
    width: 100%;
  }

  &.c_pages {
    margin-bottom: 0;
  }

  .application {
    &_main {
      padding-bottom: 87px;
      @include g.mq() {
        padding-bottom: 80px;
      }
    }

    &_headline {
      font-size: g.rem(18);
      font-weight: 500;
      letter-spacing: 0.1em;
      text-align: center;
      @include g.mq() {
        display: none;
      }
    }

    &_sec {
      &:not(.first) {
        margin-top: 116px;
        @include g.mq() {
          margin-top: 76px;
        }
      }
    }

    &_table {
      overflow: auto;
      @include g.mq() {
        overflow-x: scroll;
        padding-bottom: 20px;

        &::-webkit-scrollbar {
          height: 10px !important;
          -webkit-overflow-scrolling: auto;
          background: transparent !important;
        }

        /* Track */
        &::-webkit-scrollbar-track {
          -webkit-border-radius: 5px !important;
          border-radius: 5px !important;
          background: #e6e6e6 !important;
          -webkit-box-shadow: none !important;
        }

        /* Handle */
        &::-webkit-scrollbar-thumb {
          -webkit-border-radius: 5px !important;
          border-radius: 5px !important;
          background: #a5a4a4 !important;
          -webkit-box-shadow: none !important;
        }
        &::-webkit-scrollbar-thumb:window-inactive {
          background: #a5a4a4 !important;
        }
      }
      & > * {
        min-width: 1000px;
        @include g.mq {
          min-width: 830px;
        }
      }
      &.table1 {
        width: calc(100% + 8px);
        margin-left: -4px;

        table,
        td,
        tr {
          border: 4px solid white;
        }

        td {
          background-color: #f5f5f5;
        }

        .col_ttl {
          background-color: #d5d5d5;
        }

        .row_ttl {
          padding-left: 30px;
        }
      }

      &.table2 {
        &:not(.first) {
          margin-top: 57px;
          @include g.mq() {
            margin-top: 44px;
          }
        }

        tr {
          border-top: 1px solid #d5d5d5;
          border-bottom: 1px solid #d5d5d5;
        }

        td {
          padding-top: 24px;
          padding-bottom: 24px;
          @include g.mq() {
            padding-top: 18px;
            padding-bottom: 18px;
          }
        }

        .col_ttl {
          background-color: #f5f5f5;
          border-right: 1px solid #d5d5d5;
          width: 26.4%;
        }

        .row_ttl {
          padding: 13px 10px 13px 27px;
          @include g.mq() {
            padding-top: 11px;
            padding-bottom: 11px;
          }
        }

        table {
          margin-top: 10px;
        }

        .col_content {
          padding-left: 55px;
          // letter-spacing: ;
        }
      }

      .f_en {
        font-family: "Barlow", sans-serif;
      }

      .col_flex {
        display: flex;
        align-items: center;

        & > * {
          &:nth-child(2) {
            font-size: g.rem(14);
            letter-spacing: 0.05em;
            margin-left: 20px;
            @include g.mq() {
              font-size: g.rem(10);
            }
          }
        }
      }

      .col_label {
        border: 1px solid #23a4bd;
        display: flex;
        align-items: center;

        & > * {
          &:first-child {
            height: 25px;
            display: flex;
            align-items: center;
            background-color: #23a4bd;
            color: white;
            padding: 0 14px;
            @include g.mq() {
              height: 20px;
            }
          }

          &:last-child {
            padding: 0 18px;
          }
        }
      }

      table {
        width: 100%;
        td {
          font-weight: 400;
          letter-spacing: 0.1em;
          font-size: g.rem(16);
          padding: 22px 10px;
          text-align: center;
          @include g.mq() {
            font-size: g.rem(12);
            padding: 19px 10px;
          }
        }
      }

      .row_head {
        td {
          &:first-child {
            width: 18%;
          }

          &:nth-child(2) {
            width: 41.4%;
          }

          &:nth-child(3) {
            width: 20%;
          }

          &:last-child {
            width: #{100% - 18% - 41.4% - 20%};
          }
        }
      }

      .col_content {
        text-align: left;
        padding-left: 42px;
      }

      .row_ttl {
        color: white;
        font-size: g.rem(16);
        text-align: left;
        letter-spacing: 0.1em;
        @include g.mq() {
          font-size: g.rem(12);
        }

        &.is_blue {
          background-color: #0c63a3;
        }

        &.is_green {
          background-color: #8db10b;
        }

        &.is_orange {
          background-color: #dd6e1f;
        }

        &.is_other {
          background-color: #037398;
        }
      }
    }

    &_box {
      margin: 36px 0 0;
      background-color: #f5f5f5;
      padding: 28px 49px;
      @include g.mq() {
        padding: 28px 22px;
      }
      &_ttl {
        font-weight: bold;
        font-size: g.rem(20);
        line-height: math.div(29, 20);
        letter-spacing: 0.1em;
        @include g.mq() {
          font-size: g.rem(18);
          line-height: math.div(26, 18);
        }
      }

      &_desc {
        letter-spacing: 0.05em;
        font-size: g.rem(16);
        font-weight: 400;
        margin: 10px 0 0;
        @include g.mq() {
          margin-top: 21px;
        }
      }
    }

    &_board {
      overflow: auto;
      @include g.mq() {
        overflow-x: scroll;
        padding-bottom: 20px;
        &::-webkit-scrollbar {
          height: 10px !important;
          background: transparent !important;
          -webkit-overflow-scrolling: auto;
        }

        /* Track */
        &::-webkit-scrollbar-track {
          -webkit-border-radius: 5px !important;
          border-radius: 5px !important;
          background: #e6e6e6 !important;
          -webkit-box-shadow: none !important;
        }

        /* Handle */
        &::-webkit-scrollbar-thumb {
          -webkit-border-radius: 5px !important;
          border-radius: 5px !important;
          background: #a5a4a4 !important;
          -webkit-box-shadow: none !important;
        }
        &::-webkit-scrollbar-thumb:window-inactive {
          background: #a5a4a4 !important;
        }
      }
      table {
        font-size: g.rem(13);
        font-weight: 400;
        min-width: 1000px;
        th {
          background-color: #d5d5d5;
        }
      }

      tr {
        td:first-child {
          background-color: #75c9d9;
          color: white;
          font-weight: 500;
          letter-spacing: 0.05em;
          &.is_dark {
            background-color: #23a4bd;
          }
        }
      }

      th,
      td {
        text-align: center;
      }

      td {
        border: 1px solid #a5a4a4;
        height: 50px;
      }

      th {
        border-right: 1px solid #a5a4a4;
        border-left: 1px solid #a5a4a4;
        height: 60px;
        font-weight: 500;
        letter-spacing: 0.05em;
        line-height: math.div(19, 13);

        &:nth-child(1) {
          width: 8.6%;
        }

        &:nth-child(2) {
          width: 8.6%;
        }

        &:nth-child(3) {
          width: 8.7%;
        }

        &:nth-child(4) {
          width: 8.7%;
        }

        &:nth-child(5) {
          width: 8.8%;
        }

        &:nth-child(6) {
          width: 22.6%;
        }

        &:nth-child(7) {
          width: 11.2%;
        }

        &:nth-child(8) {
          width: 11.5%;
        }
      }
    }
    &_notice {
      font-size: g.rem(13);
      margin-top: 19px;
      @include g.mq() {
        margin-top: 39px;
      }
      &_ttl {
        display: table;
        color: white;
        background-color: #23a4bd;
        padding: 8px 4px 8px 11px;
        font-weight: 500;
        letter-spacing: 0.05em;
        line-height: math.div(17, 13);
        margin-bottom: 8px;
        @include g.mq() {
          font-size: g.rem(15);
          padding-right: 13px;
        }
      }
      &_desc {
        letter-spacing: 0.05em;
        font-weight: 400;
        & > * {
          &:first-child {
            line-height: math.div(23, 13);
            margin-bottom: 17px;
          }
          &:not(:first-child) {
            font-size: g.rem(14);
            @include g.mq() {
              font-size: g.rem(13);
            }
          }
        }
      }
    }
  }
}
