@use "sass:math";
@use "./global" as g;

.entrance_policy_page {
    .c_opencampus {
        @include g.mq() {
          margin-top: 10px;
        }
      }
    .nav-page {
        padding-top: 80px;
    }
    @include g.mq() {
        .c_nav_anchor {
            margin-bottom: 0;
        }
    }
  .policy {
    &_head {
      display: flex;
      justify-content: space-between;
      margin-bottom: 96px;
      @include g.mq() {
        display: block;
        margin: 24px 0 42px;
      }
      .txt_ttl {
        color: #23a4bd;
        font-weight: bold;
        letter-spacing: 0.1em;
      }
      .txt_desc {
        letter-spacing: 0.1em;
      }
      &_content {
        margin: -4px 0 0;
        z-index: 0;
        &:first-child {
            max-width: 524px;
            margin-right: 20px;
            @include g.mq() {
                max-width: 100%;
                margin-right: 0;
            }
          .txt_ttl {
            font-size: g.rem(30);
            font-family: "Zen Kaku Gothic New", sans-serif;
            letter-spacing: 0.05em;
            line-height: math.div(57, 30);
            font-size: clamp(g.rem(20), math.div(30,1040)*100vw, g.rem(30));
            min-width: 380px;
            white-space: nowrap;
            @include g.mq() {
                font-size: g.rem(22);
                white-space: normal;
                min-width: 0;
                // word-break: keep-all;
                line-height: math.div(36,22);
                padding-right: 5px;
            }
          }
          .txt_desc {
            font-size: g.rem(16);
            font-weight: 500;
            line-height: math.div(32, 16);
            margin: 13px 0 0;
            @include g.mq() {
                font-size: g.rem(15);
                letter-spacing: 0.1em;
                line-height: math.div(28,15);
            }
          }
        }
        &:last-child {
            max-width: 446px;
            background-color: #F5F5F5;
            padding: 67px 26px 40px;
            margin-top: 5px;
            @include g.mq() {
                max-width: 100%;
                margin-top: 37px;
                padding: 49px 26px 46px;
            }
          .txt_ttl {
            font-size: g.rem(17);
            line-height: math.div(25, 17);
            @include g.mq() {
                font-size: g.rem(16);
                line-height: math.div(24,16);
            }
          }
          .txt_desc {
            line-height: math.div(25, 15);
            font-size: g.rem(15);
            font-weight: 400;
            margin: 10px 0 0;
            @include g.mq() {
                font-size: g.rem(13);
                font-weight: 500;
                letter-spacing: 0.05em;
                line-height: math.div(23,13);
            }
          }
        }
      }
    }
    &_shape {
        position: absolute;
        width: 100%;
        display: block;
        z-index: 1;
        mix-blend-mode: multiply;
        &::after {
            content: "";
            background-color: #F7F7F7;
            display: block;
            height: 100%;
            width: 100%;
        }
        &.shape_first {
            top: 0;
            left: 0;
            height: math.div(753,1500)*100vw;
            &::after {
                transform-origin: right top;
                transform: skewX(-63deg);
            }
        }
        &.shape_sm {
            right: 0;
            top: 0;
            height: math.div(215, 1500)*100vw;
            width: math.div(301, 1500)*100vw;
            &::after {
                transform-origin: left top;
                transform: skewX(54deg);
            }
        }
        &.shape_last {
            bottom: 0;
            left: 0;
            height: math.div(753,1500)*100vw;
            &::after {
                transform-origin: right bottom;
                transform: skewX(63deg);
            }
        }
      }
    &_content {
        background-color: #F7F7F7;
        padding: 100px 0 143px;
        margin-top: 83px;
        position: relative;
        @include g.mq() {
            padding: 42px 0 74px;
            margin-top: 10px;
        }
    }
    &_list {
        position: relative;
        z-index: 2;
    }
    &_item {
        &:not(:last-child) {
            margin-bottom: 57px;
        }
        &.is_blue {
            .policy_item_head {
                background-color: #0C63A3;
            }
            .content_ttl {
                color: #0C63A3;
            }
            .content_dot {
                background-color: #0C63A3;
            }
            .content_point::after {
                background-color: #0C63A3;
            }
            .content_point_ttl {
                color: #0C63A3;
            }
        }
        &.is_orange {
            .policy_item_head {
                background-color: #DD6E1F;
            }
            .content_ttl {
                color: #DD6E1F;
            }
            .content_dot {
                background-color: #DD6E1F;
            }
            .content_point_ttl {
                color: #DD6E1F;
            }
            .content_point::after {
                background-color: #DD6E1F;
            }
        }
        &.is_green {
            .policy_item_head {
                background-color: #8DB10B;
            }
            .content_ttl {
                color: #8DB10B;
            }
            .content_dot {
                background-color: #8DB10B;
            }
            .content_point_ttl {
                color: #8DB10B;
            }
            .content_point::after {
                background-color: #8DB10B;
            }
        }
        &_head {
            display: flex;
            color: white;
            height: 92px;
            align-items: center;
            padding: 15px 40px;
            @include g.mq() {
                display: block;
                height: auto;
                padding: 15px 26px;
            }
            .txt_ttl {
                font-size: g.rem(22);
                letter-spacing: 0.05em;
                font-weight: bold;
                padding-right: 35px;
                margin-right: 33px;
                height: 100%;
                display: flex;
                align-items: center;
                border-right: 2px solid white;
                @include g.mq() {
                    padding: 0 0 5px;
                    margin: 0;
                    display: block;
                    border-right: none;
                    border-bottom: 2px solid white;
                }
            }
            .txt_desc {
                font-weight: 500;
                letter-spacing: 0.06em;
                line-height: math.div(20,14);
                max-width: 610px;
                @include g.mq() {
                    margin-top: 10px;
                    font-size: g.rem(13);
                    line-height: math.div(19,13);
                }
            }
        }
        &_body {
            background-color: white;
            .content {
                &_list {
                    max-width: 860px;
                    padding: 70px 0;
                    margin: 0 auto;
                    @include g.mq() {
                        padding: 30px 25px 25px;
                    }
                }
                &_item {
                    &:not(:last-child) {
                        margin-bottom: 60px;
                        @include g.mq() {
                            margin-bottom: 70px;
                        }
                    }
                }
                &_main {
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    @include g.mq() {
                        display: block;
                    }
                }
                &_img {
                    max-width: 401px;
                    margin-bottom: 0;
                    margin-right: 20px;
                    @include g.mq() {
                        max-width: 100%;
                        margin-right: 0;
                    }
                }
                &_line {
                    max-width: 409px;
                    width: 100%;
                    padding: 0;
                    @include g.mq() {
                        padding-top: 28px;
                    }
                }
                &_ttl {
                    letter-spacing: 0.1em;
                    font-size: g.rem(25);
                    line-height: math.div(36,25);
                    font-weight: bold;
                    @include g.mq() {
                        font-size: g.rem(22);
                        line-height: math.div(32,22);
                    }
                    &>* {
                        display: block;
                        font-size: g.rem(16);
                        line-height: math.div(24,16);
                        letter-spacing: 0.1em;
                        margin-bottom: 8px;
                        @include g.mq() {
                            font-size: g.rem(13);
                            line-height: math.div(13,19);
                        }
                    }
                }
                &_desc {
                    font-weight: 400;
                    letter-spacing: 0.06em;
                    font-size: g.rem(16);
                    margin-top: 28px;
                    @include g.mq() {
                        margin-top: 17px;
                        font-size: g.rem(13);
                        font-weight: 500;
                        line-height: 1.4;
                        letter-spacing: 0.05em;
                    }
                    &>* {
                        display: flex;
                        &:not(:last-child) {
                            margin-bottom: 11px;
                            @include g.mq() {
                                margin-bottom: 14px;
                            }
                        }
                    }
                    // line-height: math.div(38,16);
                }
                &_dot {
                    height: 21px;
                    width: 21px;
                    display: flex;
                    border-radius: 50%;
                    flex-shrink: 0;
                    margin-right: 10px;
                    margin-top: 0.3em;
                    font-size: math.div(11,16)*1em;
                    align-items: center;
                    justify-content: center;
                    line-height: 1;
                    color: white;
                    @include g.mq() {
                        margin-top: 0;
                    }
                }
                &_point {
                    padding: 0 30px;
                    margin-top: 43px;
                    z-index: 0;
                    position: relative;
                    min-height: 126px;
                    @include g.mq() {
                        margin-top: 32px;
                        padding: 0 20px;
                    }
                    &::after {
                        content: '';
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        height: 100%;
                        z-index: -1;
                        opacity: 0.1;
                    }
                    &_inner {
                        position: relative;
                        padding: 24px 0 18px;
                        z-index: 0;
                        max-width: 800px;
                        margin: 0 auto;
                        @include g.mq() {
                            padding: 20px 0;
                        }
                    }
                    &_ttl {
                        font-family: 'Barlow', sans-serif;
                        font-size: g.rem(20);
                        font-weight: bold;
                        letter-spacing: 0.08em;
                        line-height: 1;
                        position: absolute;
                        top: 0;
                        left: 0;
                        transform: translateY(-50%);
                        z-index: 1;
                        text-transform: uppercase;
                    }
                    &_desc {
                        letter-spacing: 0.06em;
                        line-height: math.div(25,14);
                        font-weight: 400;
                        @include g.mq() {
                            font-weight: 500;
                            font-size: g.rem(13);
                            line-height: math.div(23,13);
                        }
                    }
                }
            }
        }
    }
  }
}

.policy_main ~ .bread_clumb {
    display: none;
}
