@use "sass:math";
@use "./global" as g;
.point_page{
    padding-bottom: 111px;
    @media only screen and (max-width: 768px) {
        padding-bottom: 30px;
    }
    #tmp_mainvisual , .c_mainvisual_img{
        margin-bottom: 0;
    }
    .c_mainvisual_content{
        padding-top: 8px;
    }
    .c_mainvisual_ttl{
        font-size: g.rem(40);
        text-align: center;
        @media only screen and (max-width: 768px) {
            font-size: g.rem(24);
        }
    }
    .c_mainvisual_desc{
        text-align: center;
        font-size: g.rem(16);
        font-weight: 600;
        @media only screen and (max-width: 768px) {
            font-size: g.rem(14);
        }
    }
}
.c_intro{
    padding:80px 0 115px;
    @media only screen and (max-width: 768px) {
        padding: 60px 0 40px;
    }
}
.point_anchors{
    background: #262626;
    @media only screen and (max-width: 768px) {
        background: none;
        margin-bottom: 40px;
    }
    &_list{
        display: flex;
        justify-content: space-between;
        max-width: 1040px;
        padding: 0 20px;
        margin: 0 auto;
        @media only screen and (max-width: 768px) {
            padding: 0;
            flex-wrap: wrap;
        }
    }
    &_item{
       flex:1;
       overflow: hidden;
       @media only screen and (max-width: 768px) {
        flex: inherit;
        width: 100%;
        margin-bottom: 11px;
        padding:0 min((20/320)*100vw,25px);
        &:last-child{
            margin-bottom: 0;
        }
       }
        > a{
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;
            text-align: center;
            width: 100%;
            height: 100%;
            padding: 20px g.clamped(5,10,769,1500);
            @media only screen and (min-width: 769px) {
                transition: all 0.5s;
                &:hover{
                    opacity: 0.7;
                }
            }
            @media only screen and (max-width: 768px) {
                background: #262626;
                padding: 21px 10px 20px;
            }
            &::before{
                position: absolute;
                content: '';
                left: 0;
                background: #fff;
                height: calc(100% - 26px);
                top: 50%;
                transform: translateY(-50%);
                width: 1px;
                @media only screen and (max-width: 768px) {
                    content: none;
                }
            }

        }
        &:last-child{
            >a::after{
                position: absolute;
                content: '';
                right: 0;
                background: #fff;
                height: calc(100% - 26px);
                top: 50%;
                transform: translateY(-50%);
                width: 1px; 
                @media only screen and (max-width: 768px) {
                    content: none;
                }
            }
        }
        .number{
            position: absolute;
            top: -107px;
            left: 11px;
            font-family: "Barlow", sans-serif;
            font-size: 184px;
            font-weight: bold;
            letter-spacing: 0.05em;
            color: #fff;
            opacity: 0.2;
            z-index: 2;
            @media only screen and (max-width: 768px) {
                left: 0;
            }
        }
        .ttl{
            position: relative;
            font-family: 'Zen Kaku Gothic New', sans-serif;
            font-size: g.rem(16);
            font-weight:bold;
            letter-spacing: 0.1em;
            color: #fff;
            display: inline-block;
            padding-bottom: 31px;
            line-height: 1.5em;
            &::after{   
                position: absolute;
                content: '';
                bottom:0;
                left: 50%;
                transform: translateX(-50%);
                background: url(../images/school/point/icon_arrow01.svg) no-repeat center/contain;
                width: 23px;
                height: 23px;
            }
        }
    }
}
.features{
    position: relative;
    .c_container{
        position: relative;
        z-index: 2;
    }
    &::before{
        position: absolute;
        top: 0;
        right: 0;
        content: '';
        clip-path: polygon(100% 0, 0 0, 100% 100%);
        -webkit-clip-path: polygon(100% 0, 0 0, 100% 100%);
        background: #0C63A3;
        width: g.clamped(150,250,769,1500);
        height: g.clamped(150,250,769,1500);
        @media only screen and (max-width: 768px) {
            width: 105px;
            height: 105px;
        }
    }
    &::after{
        position: absolute;
        bottom: 0;
        left: 0;
        content: '';
        background: rgba(12, 99, 163,0.05);
        width: 100%;
        height: 620px;
        clip-path: polygon(100% 0, 0 100%, 100% 100%);
        -webkit-clip-path: polygon(100% 0, 0 100%, 100% 100%);
        mix-blend-mode: multiply;
        -webkit-mix-blend-mode: multiply;
        @media only screen and (max-width: 768px) {
            height: 190px;
        }
    }
    &_heading_num{
        width: g.clamped(175,219,769,1500);
        flex-shrink: 0;
        margin: 0 g.clamped(30,81,769,1500) 0 0;
        @media only screen and (max-width: 768px) {
            width: 103px;
            margin: 0 0 30px;
        }
    }
    &_area{
        position: relative;
        display: flex;
        justify-content: space-between;
        z-index: 2;
        @media only screen and (max-width: 768px) {
            flex-wrap: wrap;
        }
        .features_info{
            padding-top: 7px;
            @media only screen and (max-width: 768px) {
                padding-top: 0;
            }
        }
    }
    &_heading{
        margin-bottom: 23px;
   
        .heading_sm, .heading_b{
            font-family: 'Zen Kaku Gothic New', sans-serif;
            font-weight: bold;
            letter-spacing: 0.1em;
            color: #fff;
            display: inline-block;
            background: #0C63A3;
        }
        .heading_sm{
            font-size: g.rem(18);
            padding: 5px;
            margin-bottom: 10px;
            line-height: 1.2em;
            @media only screen and (max-width: 768px) {
                font-size: g.rem(12);
            }
        }
        .heading_b{
            padding: 5px 5px 6px;
            font-size: g.clamped(34,38,769,1500);
            line-height: 1.2em;
            @media only screen and (max-width: 768px) {
                font-size: g.rem(22);
                padding: 3px 5px 5px;
            }
        }
    }
    &_desc{
        font-size: g.rem(16);
        font-weight: 400;
        letter-spacing: 0.1em;
        @media only screen and (max-width: 768px) {
            font-size: g.rem(13);
            font-weight: 500;
            letter-spacing: 0.05em;
        }
    }
    &_video{
        position: relative;
        height: 563px;
        width: 100%;
        max-width: 1000px;
        margin: 0 auto 94px;
        @media only screen and (max-width: 1024px) {
            height: 56vw;
        }
        @media only screen and (max-width: 768px) {
            height: 51vw;
            margin: 0 auto 40px;
        }
        @media only screen and (max-width: 375px) {
            height: 49vw;
        }
        .btn_play{
            width: 91px;
            height: 90px;
            position: absolute;
            top:50%;
            left:50%;
            transform: translate(-50%,-50%);
            z-index: 4;
            @media only screen and (min-width: 769px) {
                transition: all 0.5s;
                cursor: pointer;
                &:hover{
                    opacity: 0.8;
                }
            }
            @media only screen and (max-width: 768px) {
                width: 45px;
                height: 45px;
            }
        }
        iframe{
            width: 100%;
            height: 100%;
            outline: 0;
        }
        .mask{
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            content: '';
            background: #B2B2B2;
            mix-blend-mode: multiply;
            -webkit-mix-blend-mode: multiply;
            z-index: 3;
        }
        .bg{
            position: absolute;
            content: '';
            top: 0;
            left: 0;
            background: url(../images/school/point/bg_mask_video.jpg) no-repeat center/cover;
            width: 100%;
            height: 100%;
            z-index: 2;
        }
    }
    &_first{
        padding: 127px 0 122px;
        background: #EFF6FC;
        @media only screen and (max-width: 768px) {
            padding: 60px 0;
        }
        .features_area{
            margin-bottom: 61px;
        }
        &_img0l{
            max-width: 1000px;
            width: 100%;
            margin:0 auto 83px;
            line-height: 0;
            @media only screen and (max-width: 768px) {
                margin: 0 auto 60px;
            }
        }
        &_imgs{
            display: flex;
            justify-content: space-between;
            @media only screen and (max-width: 768px) {
                flex-wrap: wrap;
            }
        }
        &_img{
            width: 31%;
            @media only screen and (max-width: 768px) {
                width: 100%;
                margin-bottom: 40px;
                &:last-child{
                    margin-bottom: 0;
                }
            }
            .img{
                text-align: center;
                width: 100%;
                margin-bottom: 18px;
            }
            .desc{
                font-size: g.rem(16);
                font-weight: 400;
                letter-spacing: 0.05em;
                color: #262626;
                @media only screen and (max-width: 768px) {
                    font-size: g.rem(13);
                    font-weight: 500;
                }
            }
            &:nth-child(2){
                .desc{
                    letter-spacing: 0.02em;
                }
            }
        }
    }
    &_second{
        padding: 131px 0 g.clamped(100,104,769,1500);
        background: #F9FDEB;
        &::before{
            background: #8DB10B;
        }
        &::after{
            background: rgba(141, 177, 11,0.05);
            mix-blend-mode: multiply;
            -webkit-mix-blend-mode: multiply;
        }
        @media only screen and (max-width: 768px) {
            padding: 60px 0;
        }
        .features_area{
            margin-bottom: 64px;
            .features_info {
                padding-top: 1px;
            }
        }
        .features_heading{
             .heading_sm, .heading_b{
                background: #8DB10B;
            }
        }
        .features_heading_num{
            width: g.clamped(190,245,769,1500);
            margin: 0 g.clamped(30,55,769,1500) 0 0;
            @media only screen and (max-width: 768px) {
                width: 121px;
                margin: 0 0 30px;
            }
        }
        &_imgs{
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            margin-bottom: 40px;
            @media only screen and (max-width: 768px) {
                margin-bottom: 45px;
            }
        }
        &_img{
            width: 46%;
            margin-bottom: 40px;
            @media only screen and (max-width: 768px) {
                width: 100%;
                margin-bottom: 30px;
                &:last-child{
                    margin-bottom: 0;
                }
            }
            .img{
                text-align: center;
                width: 100%;
                margin-bottom: 20px;
                @media only screen and (max-width: 768px) {
                    margin-bottom: 15px;
                }
            }
            .ttl{
                font-size: g.clamped(18,20,769,1500);
                font-weight: bold;
                letter-spacing: 0.05em;
                color: #262626;
                line-height: 1.5em;
                @media only screen and (max-width: 768px) {
                    font-size: g.rem(15);
                }
            }
        }
        &_frame{
            display: flex;
            align-items: center;
            background: #fff;
            padding:35px g.clamped(30,55,769,1500) 35px g.clamped(30,72,769,1500);
            margin-bottom: 60px;
            @media only screen and (max-width: 768px) {
                flex-wrap: wrap;
                padding: 40px 33px;
                margin-bottom: 40px;
            }
            .img{
                width: g.clamped(210,266,769,1500);
                flex-shrink: 0;
                margin: 0 g.clamped(30,85,769,1500) 0 0;
                @media only screen and (max-width: 768px) {
                    margin: 0 auto 15px;
                    text-align: center;
                    width: 100%;
                }
            }
            &_info{
                padding-top: 3px;
                .ttl{
                    line-height: 1.5em;
                    font-family: 'Zen Kaku Gothic New', sans-serif;
                    font-size: g.rem(26);
                    font-weight: bold;
                    color: #000;
                    letter-spacing: 0.05em;
                    margin-bottom: 7px;
                    @media only screen and (max-width: 768px) {
                        font-size: g.rem(22);
                        letter-spacing: 0.1em;
                        margin-bottom: 10px;
                    }
                }
                .desc{
                    font-size: g.rem(16);
                    font-weight: 400;
                    letter-spacing: 0.05em;
                    color: #262626;
                    @media only screen and (max-width: 768px) {
                        font-size: g.rem(13);
                        font-weight: 500;
                    }
                }
            }
        }
        &_seminars{
            padding: 0 5px;
            @media only screen and (max-width: 768px) {
                padding: 0;
            }
            .ttl{
                color: #8DB10B;
                font-family: 'Zen Kaku Gothic New', sans-serif;
                font-weight: bold;
                font-size: g.rem(24);
                line-height: 1.5em;
                margin-bottom: 26px;
                letter-spacing: 0.05em;
                @media only screen and (max-width: 768px) {
                    font-size: g.rem(22);
                }
            }
        }
        &_list{
            display: flex;
            @media only screen and (max-width: 1024px) {
                flex-wrap: wrap;
            }
        }
        &_item{
            flex-shrink: 0;
            margin: 0 42px 42px 0;
            @media only screen and (max-width: 1024px) {
                margin: 0 20px 42px 0;
                width: 47%;
            }
            @media only screen and (max-width: 768px) {
                width: 100%;
                margin: 0 0 20px;
            }
            &:last-child{
                margin: 0;
            }
            .ttl_sm{
                color: #262626;
                font-family: 'Zen Kaku Gothic New', sans-serif;
                font-weight: bold;
                font-size: g.rem(18);
                letter-spacing: 0.05em;
                line-height: 1.5em;
                margin-bottom: 10px;
            }
        }
        &_txts{
            li{
                font-size: g.rem(12);
                position: relative;
                display: flex;
                padding-left:12px;
                line-height: 1.5em;
                letter-spacing: 0;
                font-weight:400;
                margin-bottom: 2px;
                &:last-child{
                    margin-bottom: 0;
                }
                &::before{
                    position: absolute;
                    top: 4px;
                    left: 0;
                    content: "";
                    border-radius: 50%;
                    width: 10px;
                    height: 10px;
                    background: #262626;
                    @media only screen and (max-width: 768px) {
                        top: 5px;
                    }
                }
            }
        }
    }
    &_third{
        padding:125px 0 g.clamped(100,171,769,1500);
        background: rgba(221, 110, 31,0.05);
        &::before{
            background: #DD6E1F;
        }
        &::after{
            background: rgba(221, 110, 31,0.05);
            mix-blend-mode: multiply;
            -webkit-mix-blend-mode: multiply;
        }
        @media only screen and (max-width: 768px) {
            padding: 60px 0;
        }
        .features_area{
            margin-bottom: 60px;
        }
        .features_heading{
             .heading_sm, .heading_b{
                background: #DD6E1F;
            }
        }
        .features_heading_num{
            width: g.clamped(200,252,769,1500);
            margin: 0 g.clamped(30,48,769,1500) 0 0;
            @media only screen and (max-width: 768px) {
                width: 171px;
                margin: 0 0 30px;
            }
        }
        &_caption{
            text-align: center;
            margin-bottom: 40px;
            .wrap_caption{
                color: #fff;
                position: relative;
                display: inline-block;
                text-align: center;
                padding: 13px 57px 13px 58px;
                background: #262626;
                border-radius: 34px;
                font-family: 'Zen Kaku Gothic New', sans-serif;
                font-weight: 500;
                letter-spacing: 0.1em;
                font-size: g.rem(25);
                @media only screen and (max-width: 768px) {
                    font-size: g.rem(13);
                    padding: 7px 15px 8px 21px;
                }
                &::after{
                    position: absolute;
                    content: "";
                    top: 100%;
                    left: 50%;
                    transform: translateX(-50%);
                    border-top: solid 18px #262626;
                    border-left: solid 9px transparent;
                    border-right: solid 9px transparent;
                    @media only screen and (max-width: 768px) {
                        border-top: solid 8px #262626;
                        border-left: solid 5px transparent;
                        border-right: solid 5px transparent;
                    }
                }
            }
        }
        &_list01, &_list02{
            display: flex;
            justify-content: space-between;
            @media only screen and (max-width: 768px) {
                flex-wrap: wrap;
            }
        }
       
        &_info{
            margin-bottom: 36px;
            @media only screen and (max-width: 768px) {
                margin-bottom: 30px;
            }
            &:last-child{
                margin-bottom: 0;
            }
            .ttl{
                color: #DD6E1F;
                font-family: 'Zen Kaku Gothic New', sans-serif;
                font-weight:bold;
                font-size: g.rem(20);
                line-height: 1.5em;
                margin-bottom: 8px;
                letter-spacing: 0.05em;
                @media only screen and (max-width: 1024px) {
                    letter-spacing: 0;
                }
                @media only screen and (max-width: 768px) {
                    letter-spacing: 0.05em;
                    font-size: g.rem(16);
                }
            }
            .desc{
                font-weight: 400;
                font-size: g.rem(16);
                letter-spacing: 0.05em;
                color: #262626;
                @media only screen and (max-width: 768px) {
                    font-size: g.rem(13);
                    font-weight: 500;
                }
            }
           
        }
        &_list01{
            margin-bottom: 113px;
            @media only screen and (max-width: 768px) {
                 margin-bottom: 30px;
            }
            .features_third_l{
                width: 46%;
                @media only screen and (max-width: 768px) {
                    width: 100%;
                    margin-bottom: 40px;
                }
                .img{
                    margin-bottom: 22px;
                    @media only screen and (max-width: 768px) {
                        margin-bottom: 10px;
                    }
                    &:last-child{
                        margin-bottom: 0;
                    }
                }
            }
            .features_third_r{
                width:48.8%;
                padding-top: g.clamped(5,31,769,1500);
                @media only screen and (max-width: 768px) {
                    padding-top: 0;
                    width: 100%;
                }
                .features_third_info:nth-child(2){
                    .desc{
                        letter-spacing: 0;
                    }
                }
            }
        }
        &_list02{
            .features_third_l{
                width: 46%;
                @media only screen and (max-width: 768px) {
                    width: 100%;
                    margin-bottom: 40px;
                }
            }
            .features_third_r{
             
                width:48.8%;
                display: flex;
                justify-content: space-between;
                @media only screen and (max-width: 768px) {
                    width: 100%;
                }
                .img{
                    width: 47.6%;
                }
            }
        }
    }
}