@use "sass:math";
@use "global" as g;


.entrance_page {
    padding-bottom: 237px;
    @include g.mq() {
        padding-bottom: 140px;
        #tmp_mainvisual {
            height: auto;
        }
    }
    .c_sec_contact {
        margin-top: 44px;
    }
    .entrance {
        &_main {
            padding-bottom: 44px;
        }
        &_sec {
            &:not(:first-child) {
                margin-top: 157px;
                @include g.mq() {
                    margin-top: 80px;
                }
            }
        }
        &_banner {
            position: relative;
            margin-top: 139px;
            display: block;
            transition: opacity 0.3s ease;
            &:hover {
                opacity: 0.8;
            }
            @include g.mq() {
                margin-top: 80px;
            }
            &_img {
                margin: 0;
            }
            &_btn {
                display: flex;
                position: absolute;
                top: 0;
                left: 0;
                height: 100%;
                width: 100%;
                color: white;
                font-weight: bold;
                font-family: 'Zen Kaku Gothic New', sans-serif;
                font-size: g.rem(29);
                letter-spacing: 0.2em;
                justify-content: center;
                align-items: center;
                width: 42%;
                @include g.mq() {
                    font-family: "Noto Sans JP", sans-serif;
                    letter-spacing: 0.1em;
                    position: static;
                    background-color: #00A7C0;
                    height: 120px;
                    width: 100%;
                    font-size: g.rem(20);
                }
                &::after {
                    content: "";
                    display: block;
                    height: 27px;
                    width: 27px;
                    background-repeat: no-repeat;
                    background-image: url(../images/entrance/icon_arrow.svg);
                    background-size: contain;
                    background-position: center;
                    flex-shrink: 0;
                    margin-left: 15px;
                    margin-top: 2px;
                }
            }
        }
    }
}





